<template>
	<!--赠送-->
	<div class='send-wrapper classBox container'>
		<div class='tit'>
			<span>赠送</span>
		</div>
		<ul class="classItems">
			<template >
				<li class='item item2'>
					<div class="classImgBox">
						<img src="./assets/images/icon.png" alt='' class="Img" @error = "_imageError"/>
					</div>
					<div class="descBox">
						<h2 class="title line1">
							<template v-if='packageDetail.sendvideo != 0'>
								{{packageDetail.sendvideo}}课时录播课
							</template>
							<template v-if='packageDetail.sendlive != 0'>
								{{packageDetail.sendlive}}课时直播课
							</template>
						</h2>
						<p class="msg">
							本套餐内容
							<template v-if='packageDetail.giftthreshold != 0'>
								购买课程满{{packageDetail.giftthreshold}}元可以使用，
							</template>
							{{packageDetail.gifttrialtype | gettrialtype}}可使用，
							需在有效期截止前使用，逾期将无法使用。</p>
						<p class='tip'>
							本券使用范围：{{packageDetail.gifttrialtype | gettrialtype}}可使用
						</p>
						<p class='tip'>
							有效期至：
							<!-- giftefftype: 有效期状态（1：时间范围 2：固定天数） -->
							<template v-if='packageDetail.giftefftype == 1'>
								{{packageDetail.gifteffendtime | getTime}}
							</template>
							<template v-if='packageDetail.giftefftype == 2'>
								购买后{{packageDetail.giftefftime}}天
							</template>
						</p>
						<!-- <p class="price"><span>价值：￥</span>888:00</p> -->
					</div>
				</li>
			</template>
		</ul>
	</div>
</template>
<script>
	export default {
		props:['packageDetail'],
	};
</script>
<style lang="less" scoped>
	@import "../../courseDetails/assets/css/courseDetails";
	@import "~@/assets/css/list2";
</style>



