<template>
  <div class="">
    <Head2 isTitleShow = 'true' title ='支付订单' isStepShow = 'true' isStepNum = '2'></Head2>
    <!-- 支付中心 -->
    <div class="paymentCenterBox container">
      <!-- 订单编号： -->
      <div class='orderTip'>
        <em></em>
        <div>
          <h2>订单提交成功，请您尽快付款！</h2>
          <p class='ordernum'>{{$t('Main.paymentCenter.ordernum')}} {{OrderDetailData.ordernum}}</p>
          <p class ='time' v-if='isShowTime'>
            订单将为你保留至{{newordertime}},请抓紧时间支付
          </p>
        </div>
      </div>

      <div class="topBox">
        <div class="logo">
          <img :src="OrderDetailData.imageurl" @error = "_imageError"/>
        </div>
        <div class="OrderInfo">
          <table  border="0" cellpadding="0" cellspacing="0">
           <template v-if='OrderDetailData.classtypes == 1'>
								<thead>
									<th>课程名称</th>
									<th width='20%'>授课老师</th>
									<th width='20%'>课程类型</th>
									<th width='20%'>课时数</th>
									<th width='15%'>价格(总)</th>
								</thead>
								<tbody>
									<td>{{OrderDetailData.coursename}}</td>
									<td>{{OrderDetailData.teachername}}</td>
									<td>{{OrderDetailData.classtypes | changeType}}</td>
									<td>{{OrderDetailData.periodnum}}</td>
									<td>{{$t('Main.Common.symbol')}}{{OrderDetailData.amount}}</td>
								</tbody>
							</template>
							<template v-if='OrderDetailData.classtypes == 2'>
								<thead>
									<th width='20%'>上课时间</th>
									<th>课程名称</th>
									<th width='15%'>授课老师</th>
									<th width='15%'>课程类型</th>
									<th width='15%'>课时数</th>
									<th width='15%'>价格(总)</th>
								</thead>
								<tbody>
									<td >
										{{OrderDetailData.starttime}}<br>
										<template v-if='OrderDetailData.starttime != OrderDetailData.endtime'>{{OrderDetailData.endtime}}</template>
									</td>
									<td>{{OrderDetailData.coursename}}</td>
									<td>{{OrderDetailData.teachername}}</td>
									<td>{{OrderDetailData.classtypes | changeType}}</td>
									<td>{{OrderDetailData.periodnum}}</td>
									<td>{{$t('Main.Common.symbol')}}{{OrderDetailData.amount}}</td>
								</tbody>
							</template>
          </table>
        </div>
      </div>

      <div class="bottomBox">
        <!-- 选择支付方式： -->
        <p class="methods">{{$t('Main.paymentCenter.methods')}}</p>
        <ul class="couponList">
          <li>
            <p class="balancePaymen">
              <label for="c1"><input type="checkbox" name="c1" id="c1" value="" v-model="checked"/>{{$t('Main.paymentCenter.balancePaymen1')}}</label>
            </p>
            <p class="accountBalance">
              {{$t('Main.paymentCenter.accountBalance')}}<span class="price">{{OrderDetailData.usablemoney}}{{$t('Main.paymentCenter.unit')}}</span>
            </p>
          </li>
        </ul>
        <ul class="payMode" :class = "{noClick:isActive}">
          <li v-for="(btn,index) in payBtn" :key="index" :class="{active: btn.active}"  @click="selectPayType(btn.payType,index)" >
            <img :src="btn.icon"/>
          </li>
        </ul>
        <div class="placeBox">
          <!-- 支付宝支付 -->
          <a :href='payurl' v-if="w == 3"  class="orderBtn" target="_blank">确认并支付</a>
          <!-- 微信支付 -->
          <button class="orderBtn" v-if="w == 2" @click="tj()" >确认并支付</button>
          <!-- 余额支付 -->
          <a v-if="w != 2 && w != 3 " href="javascript:;" class="orderBtn"  @click="tj()">确认并支付</a>
        </div>
      </div>
		</div>
		<Feet></Feet>
	</div>
</template>
<script>
import Server from "@/service/request";
import Path from "@/service/Path";
import Head2 from "@/component/header2";
import Feet from "@/component/feet";
import wxpay from "./assets/images/wxpay.png";
import alipay from "./assets/images/alipay.png";
export default {
  components: {
    Head2,
    Feet
  },
  data() {
    return {
      isShowTime:true, //订单保留时间
      newordertime:'',
      OrderDetailData: {},//订单详情
      checked: false,
      isActive:true,//是否可选择其他方式
      payBtn:[
        {
          id:0,
          name:'wxpay',
          payType:2,
          icon:wxpay,
          active:false
        },
        {
          id:1,
          name:'zhifubao',
          payType:3,
          icon:alipay,
          active:false,
        },
      ],
      ordernum:'',//订单编号
      y:0, // y : 选中余额
      w:0, // w: 选中的第三方支付方式，2 微信 3支付宝
      payurl:'', //支付宝支付跳转链接
      typearr:'',//支付方式集合
      yubalance:0,//余额
      ordermoney:0,//订单金额
    };
  },
  watch:{
    //监听余额支付选中状态  
    checked(val){
      let {w,yubalance,ordermoney} = this;
      if (val) {
        if (yubalance >= ordermoney && w > 0) { //余额>=订单金额 && 已选中其他支付方式时，取消选中其他支付方式
          this.w = 0;
          this.payBtn = this.payBtn.map((el,index)=>{
            el.active = false;
            return el;
          })
        }
        this.y = 1;
      } else {
        this.y = 0;
      }
      // 计算最终支付方式
      this.getType()
    },
  },
  created() {
    this.ordernum = this.$route.query.ordernum
    this.showOrderDetail();
  },
  methods: {
    //获取订单详情
    showOrderDetail() {
      let {ordernum} = this
      Server.post( Path.showOrderDetail, {
          ordernum: ordernum
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
            this.OrderDetailData = data; //所有的数据;
            this.calcDate(data.ordertime)
            this.yubalance = parseFloat(this.OrderDetailData.usablemoney); //余额
            this.ordermoney = parseFloat(this.OrderDetailData.amount);//订单金额
            this.getPayType();
          }else{
            this.$message.error(res.info);
          }
        }
      );
    },
    //计算默认支付方式选中
    getPayType(){
      let {yubalance,ordermoney} = this;
      if (yubalance >= ordermoney) { //余额>订单今额，默认选中余额支付
        this.checked = true;
      }
      if (Number(ordermoney) == 0) { //订单今额为0，默认且只能选择余额支付
        this.checked = true;
        this.isActive = true;
      }else{
        this.isActive = false;
      }
    },
    // 选择微信支付or支付宝支付 w：2微信 3支付宝
    selectPayType(type,index) {
      let { payBtn, y , w ,yubalance,ordermoney} = this;
      //余额>=订单今额 && 选中余额支付时，取消选中余额支付
      if (yubalance >= ordermoney && y > 0) { 
        this.checked = false;
      }
      // 切换高亮状态
      this.payBtn = payBtn.map((el,index)=>{
        if(el.payType == type){
          el.active = true;
        }else{
          el.active = false;
        }
        return el;
      })
      // 给其他支付方式赋值
      if(index == 1){
        this.w =3;
      }else if(index == 0){
        this.w =2;
      }
      // 计算最终支付方式
      this.getType();
    },
    // 计算支付方式
    /*
      如果订单金额=0，默认且只能选择余额支付。
      如果余额>=订单金额，默认选中余额支付。并且只能选择一种支付方式。
      如果余额 <订单金额, 默认不选中支付方式，可选微信支付或支付宝支付或与余额混合支付。
    */
    getType(){
      let { y , w,ordernum,yubalance,ordermoney} = this;
      if(yubalance >= ordermoney){ //余额>=订单今额,单一支付方式
        if(y <= 0){
          this.typearr = `${w}`; // 微信或者支付宝
        }else{
          this.typearr = `${y}`; //余额支付
        }
      }else{ //余额<订单今额,单一支付方式或混合支付。没选中余额，只能单一支付;选中余额，混合支付
        if(y <= 0){
          this.typearr = `${w}`; 
        }else{
          if(this.yubalance == 0){
            this.typearr = `${w}`; // 微信或支付宝
          }else{
            this.typearr = `${y},${w}`; // 微信支付宝是否选择 + 余额
          }
        }
      };
      // console.log(y,w,this.typearr)
      // 支付宝制度 跳转链接
      this.payurl =this.$webIp+'/web/PayMiddle/index.html?ordernum='+ordernum+'&paytype='+this.typearr+'&type=alipayWeb&buyClass=2';
    },
    //提交订单
    tj() {
      let {y,w,typearr,ordernum,yubalance ,ordermoney}=this;
      //选择支付方式
      if(y <= 0  && w <= 0){
        this.$message.warning(this.$t('Main.paymentCenter.tip1'));
        return false;
      };
      //余额支付 余额不足 不提交订单
      if(y > 0 && w <= 0){  
        if(yubalance - ordermoney < 0 ){ //余额小于订单金额
          this.$message.warning(this.$t('Main.paymentCenter.tip2'));
          return false;
        };
      };
      //微信支付
      if(w == 2){
        let money;
        if(y == 0){
          money = 0
        }else{
          money = yubalance
        }
        let routeData = this.$router.resolve(
          { 
            path: '/micro', 
            query: {  
              order: ordernum, 
              paytype:typearr,
              money:money,
              buyClass:2,//1套餐2课程
            } 
          }
        );
        window.open(routeData.href, '_blank');
        return false;
      };
      // 余额支付
      if(y > 0 && w <= 0){
        // window.localStorage.setItem('buyClass',1) //1套餐 2课程
        Server.get(Path.gotoPay,{
            ordernum: ordernum,
            paytype:typearr,
          },
          res => {
            let { code, data } = res;
            if (code === 0) {
              this.$router.push({
                path: "/paymentSuccess",
                query:{
                  type:0,  //支付成功
                  order: ordernum, 
                  buyClass:2,//1套餐2课程
                }
              });
            }else{
              this.$router.push({
                path: "/paymentSuccess",
                query:{
                  type:1, //支付失败
                  order: ordernum, 
                  buyClass:2,//1套餐2课程
                }
              });
            }
          }
        );
      }
    },
    //提醒订单失效时间
    calcDate(dater) {
      var newDate=new Date(dater);
      newDate = newDate.setMinutes(newDate.getMinutes()+30);
      function add0(m){return m<10?'0'+m:m }
      //ijianchuo是整数，否则要parseInt转换
      var time = new Date(newDate);
      var y = time.getFullYear();
      var m = time.getMonth()+1;
      var d = time.getDate();
      var h = time.getHours();
      var mm = time.getMinutes();
      var s = time.getSeconds();
      // return y+'-'+add0(m)+'-'+add0(d)+' '+add0(h)+':'+add0(mm)+':'+add0(s);
      this.isShowTime = true;
      return this.newordertime = add0(h)+':'+add0(mm)+':'+add0(s);
    },
  },
};
</script>

<style lang="less" scoped>
@import "./assets/css/paymentCenter.less";
@import "../placeOrder/assets/css/placeOrder.less";
.orderBtn {
  display: block;
}
.noClick{
  pointer-events: none;
}
</style>



