<!--头部状态栏-->
<template>
    <!--  @mouseleave="showuserlist = false" -->
    <div  class='head-top'>
        <div class='container'>
            <ul class='menu-list right'>
                <template  v-if="showuser">
                    <li class='list'  @click="changeactive(0)">收藏夹</li>
                    <li class='list'  @click="changeactive(0)">我的课表</li>
                    <li class='list'><a :href='url4' target="_blank">老师登录</a></li>
                </template>
                <template  v-else>
                    <li class='list'><a :href='url3' target="_blank">收藏夹</a> </li>
                    <li class='list'><a :href='url' target="_blank">我的课表</a></li>
                    <li class='list'><a :href='url4' target="_blank">老师登录</a></li>
                </template>
                <li class='list'>
                    <router-link to="gywm">关于我们</router-link>
                </li>
            </ul>
            <div class='login-list right'>
                <ul class="register" v-if="showuser">
                    <li @click="changeactive(1)">注册有礼</li>
                    <li @click="changeactive(0)">登录</li>
                </ul>
                <ul class="usertitle" v-else @mouseover="showuserlist = true">
                    <!-- <li><DefaultImg :sourceimg="userImg" class='user-img'/></li> -->
                    <li class="name">
                        <a :href='url' target="_blank">{{username}}</a>
                    </li>
                    <li class="name" @click="exit()">退出</li>
                </ul>
            </div>
            <!-- banner login -->
            <div class='banner-login' v-if='isBannerLogin'>
                <template v-if="showuser">
                    <DefaultImg :sourceimg="userImg" class='user-img'/>
                    <p class="name">欢迎来到柠檬教育！</p>
                    <div class='btn login-btn' @click="changeactive(0)">学员登录</div>
                    <div class='btn res-btn'  @click="changeactive(1)">注册有礼</div>
                </template>
                <template v-else>
                    <a :href='url' target="_blank">
                        <DefaultImg :sourceimg="userImg" class='user-img'/>
                    </a>
                    <p class="name">{{username}}</p>
                    <p class='exit' @click="exit()">安全退出</p>
                    <div class='icon-btn'>
                        <a :href='url' target="_blank" class='icon icon1 left'><em></em>我的课表</a>
                        <a :href='url5' target="_blank" class='icon icon2 right'><em></em>我的账户</a>
                    </div>
                </template>
            </div>
        </div>
        <!--登录弹窗-->
        <el-dialog
            :visible.sync="centerDialogVisible"
            width="390px"
            :lock-scroll='false'
            :close-on-click-modal='false'
            center>
            <div class="loginregister" >
                <div class="info">
                    <div class="login" v-if="curindex == 0">
                        <div class="topTitle">账号密码登录</div>
                        <el-form :model="loginInfo" :rules="formRules" ref="loginInfo" key='1' class="loginInfo">
                            <el-form-item  prop="loginPhone"  class='h-no-border-area'>
                                <v-areaCode @selectValue = 'selectValue'></v-areaCode>
                                <!-- 请输手机号 -->
                                <el-input v-model.trim="loginInfo.loginPhone" :placeholder="$t('Main.Login.loginPhone')" class='pdleft55'></el-input>
                            </el-form-item>
                            <el-form-item  prop="loginpwd" style='clear:both;'>
                                <!-- 请输入密码 -->
                                <el-input type="password" v-model.trim="loginInfo.loginpwd" :placeholder="$t('Main.Login.loginpwd')" @keyup.enter.native="submitForm('loginInfo')"></el-input>
                            </el-form-item>
                            <!-- 确定 -->
                            <el-button type="primary" @click="submitForm('loginInfo')" class='login-btn'>{{$t('Main.Login.loginBtn')}}</el-button>
                            <div class="fastEnter">
                                <!-- 注册 -->
                                <p  @click="changeactive(1)">立即注册</p>
                                <!-- 忘记密码 -->
                                <router-link to="/forgetpwd">{{$t('Main.Login.forgetBtn')}}?</router-link>
                            </div>
                            
                            
                        </el-form>
                    </div>
                    <div class="registers" v-if="curindex == 1">
                        <div class="topTitle">快速注册</div>
                        <el-form  :model="resInfo" :rules="resInfoRules" ref="resInfo" key='2' v-if="isregister">
                            <el-form-item  prop="resPhone" class='h-no-border-area'>
                                <v-areaCode @selectValue = 'selectValue'></v-areaCode>
                                <!-- 请输手机号 -->
                                <el-input v-model.trim="resInfo.resPhone" :placeholder="$t('Main.Login.loginPhone')" class='pdleft55' autocomplete="off"></el-input>
                            </el-form-item>
                            <el-form-item prop="resImgCode" class='res-code' style='position:relative'>
                                <!-- 请输入验证码 -->
                                <el-input v-model.trim="resInfo.resImgCode" class='code-input' :placeholder="$t('Main.Login.codeinput')" autocomplete="off"></el-input>
                                <img  :src="imgCode" alt="" @click="getimgCode()" class='code-img'/>
                            </el-form-item>
                            <el-button type="primary" @click="register('resInfo')" class='login-btn'>下一步</el-button>
                            <div class='goLogin'>已有账号?<p  @click="changeactive(0)">立即登录</p></div>
                        </el-form>
                        <div class="modify" v-else>
                            <el-form :model="resInfo2" :rules="resInfo2Rules" ref="resInfo2" key='3'>
                                <!-- 短信验证码已发送至 -->
                                <!-- <span class="sms">{{$t('Main.Login.sms')}}<b>{{resInfo.resPhone}}</b></span> -->
                                <el-form-item  prop="resMesCode" class='mes-code'>
                                    <!-- 请输入短信验证码 -->
                                    <el-input v-model.trim="resInfo2.resMesCode" :placeholder="$t('Main.Login.resMesCode')" autocomplete="off"></el-input>
                                    <!-- 秒后重新获取 -->
                                    <p class='time1' v-if="getsms">
                                        <template v-if = 'this.$i18n.locale == "zh-cn"'>
                                            {{count}}{{$t('Main.Login.getsms')}}
                                        </template>
                                        <template v-else>
                                            {{$t('Main.Login.getsms2')}} {{count}} {{$t('Main.Login.getsms2')}}
                                        </template>
                                    </p>
                                    <!-- 获取短信验证码 -->
                                    <p  class='time1' v-else @click="codeBoth2()">{{$t('Main.Login.codeBoth')}}</p>
                                </el-form-item>
                                <el-form-item prop="respwd">
                                    <!-- 请输入密码 -->
                                    <el-input type="password" v-model.trim="resInfo2.respwd" :placeholder="$t('Main.Login.loginpwd')" autocomplete="off"></el-input>
                                </el-form-item>
                               <!-- 注册 -->
                                <el-button type="primary" @click="submitForm2('resInfo2')" class='login-btn'>{{$t('Main.Login.Register')}}</el-button>
                                <div class="agreement">
                                    <!-- <input type="checkbox" name="" id="" value=""  v-model="checked" /> -->
                                    点击注册表示已阅读并同意
                                    <router-link target="_blank" :to="{path:'/agreement'}">《用户协议》</router-link>
                                </div>
                                <!-- 返回修改手机号 -->
                                <span class="back" @click="back()">{{$t('Main.Login.editPhone')}}</span>
                            </el-form>
                        </div>
                    </div>
                </div>
            </div>
        </el-dialog>
        <!-- 选择标签 -->
        <selectLabel></selectLabel>
    </div>
</template>
<script>
import "./assets/css/login.css";
import Server from "@/service/request";
import Path from "@/service/Path";
import DefaultImg from "@/component/defaultHeader";
import areaCode from '@/component/areaCode'
import selectLabel from "@/container/selectLabel";
export default {
    name:"showimg",
    props:['isBannerLogin'],
    data(){
        var validatePass = (rule, value, callback) => {
            var PasswordRegex = /^[a-zA-Z0-9_]{5,15}$/;
            if (!PasswordRegex.test(value)) {
                // '请输入6-16位新密码,字母、数字、下划线'
                callback(new Error(this.$t('Main.Login.PasswordRegex')))
            } else {
                callback();
            }
        };
        var validatePhone = (rule, value, callback) => {
            var MobileRegex = /^1\d{5,14}$/;
            if (!MobileRegex.test(value)) {
                // 请输入正确的11位手机号码
                callback(new Error(this.$t('Main.Login.MobileRegex')))
            } else {
                callback();
            }
        };
        var validateMes = (rule, value, callback) => {
            var MesRegex = /^(\d){6}$/;
            if (!MesRegex.test(value)) {
                // 请输入正确的6位验证码
                callback(new Error(this.$t('Main.Login.MesRegex')))
            } else {
                callback();
            }
        };
        var validateCode = (rule, value, callback) => {
            var ImgRegex = /^[A-Za-z0-9]{5}$/;
            if (!ImgRegex.test(value)) {
                // 请输入正确的5位验证码
                callback(new Error(this.$t('Main.Login.ImgRegex')))
            } else {
                callback();
            }
        };
        return{
            url:'',// 我的收藏
            url2:'', //我的课程
            url4:'', //老师登录
            url5:'', //我的账户

            nikename:'',
            isSelectValue: '86', //获取区号
            username:'',
            userImg:DefaultImg,

            classifiy:false,//是否显示全部分类
            centerDialogVisible: false ,// 登录模态框
            checked:true,//注册协议

            curindex:0,//显示登录框or注册
            showuser:true,//未登录 注册按钮
            showuserlist:false,//登录后快速入口
            isregister:true,//注册第一步

            loginInfo:{
                loginPhone:'',//手机号
                loginpwd:''//密码
            },
            resInfo:{
                resPhone:'',//手机号
                resImgCode:'',//图片验证码
            },
            resInfo2:{
                resMesCode:'',//短信验证码
                respwd:'', //密码
            },
            imgCode:null, //图形验证码图片
            sessionid:'',//图形验证码id

            publickey:'', //公钥

            getsms:true,
            smsshow:false,
            count:"",//60s计时器
            codeBol:false,//二维码的显示隐藏

            formRules: {
                loginpwd: [
                    // 请输入密码
                    {required: true, message: this.$t('Main.Login.loginpwd'),  trigger: 'blur'},
                    { validator: validatePass, trigger: 'blur' }
                ],
                loginPhone: [
                    // 请输入手机号码
                    {required: true, message: this.$t('Main.Login.loginPhone'),  trigger: 'blur'},
                    { validator: validatePhone, trigger: 'blur' }
                ],
            },
            resInfoRules: {
                resImgCode: [
                    // 请输入验证码
                    {required: true, message: this.$t('Main.Login.codeinput'), trigger: 'blur'},
                    // { validator: validateCode, trigger: 'blur' }
                ],
                resPhone: [
                    // 请输入手机号码
                    {required: true, message: this.$t('Main.Login.loginPhone'),  trigger: 'blur'},
                    { validator: validatePhone, trigger: 'blur' }
                ],
            },
            resInfo2Rules: {
                resMesCode: [
                    // 请输入短信验证码
                    {required: true, message: this.$t('Main.Login.resMesCode'), trigger: 'blur' },
                    { validator: validateMes, trigger: 'blur' }
                ],
                respwd: [
                    // 请输入密码
                    {required: true, message: this.$t('Main.Login.loginpwd'),  trigger: 'blur'},
                    { validator: validatePass, trigger: 'blur' }
                ],
            },
            formData:{},
        }
    },
    components:{
      DefaultImg,
      'v-areaCode':areaCode,
      selectLabel,
    },
    created(){
       this.getInfo()
       this.url4 = this.$webIp+'/teacher';
    },
    methods:{ 
        //获取信息
        getInfo(){
            if(this.$loginInfo != null || this.$loginInfo != undefined){
                this.url = this.$webIp+'/student/middleWare/index.html?type=1&studentinfo='+JSON.stringify(this.$loginInfo)+'&key='+window.localStorage.getItem('studentkey');
                this.url2 = this.$webIp+'/student/middleWare/index.html?type=2&studentinfo='+JSON.stringify(this.$loginInfo)+'&key='+window.localStorage.getItem('studentkey');
                this.url3 = this.$webIp+'/student/middleWare/index.html?type=3&studentinfo='+JSON.stringify(this.$loginInfo)+'&key='+window.localStorage.getItem('studentkey');
                this.url5 = this.$webIp+'/student/middleWare/index.html?type=7&studentinfo='+JSON.stringify(this.$loginInfo)+'&key='+window.localStorage.getItem('studentkey');
                this.showuser = false;
                let uid = this.$loginInfo.uid;
                this.getStudentInfo(uid);
            } else{
                this.showuser = true;
            }
        },
        //获取区号
        selectValue(val){
            this.isSelectValue = val;
        },
        // 获取个人信息
        getStudentInfo(uid) {
            Server.post(Path.getStudentInfo,
                {
                    studentid:uid
                },
                res => {
                    let {code,data}=res;
                    if (code === 0) {
                        this.nikename = data.nickname;
                        this.username =data.nickname;
                        this.userImg = data.imageurl;
                        let obj = JSON.parse(window.localStorage.getItem('studentinfo'));
                        obj.imageurl = data.imageurl;
                        obj.nickname = data.nickname;
                        window.localStorage.setItem('studentinfo',JSON.stringify(obj));
                    }else{
                        this.$message({
                            message:res,info,
                            type: 'error'
                        });
                    }
                }
            );
        },
       //点击登录还是注册
        changeactive(index){
          this.loginInfo = { loginPhone:'', loginpwd:''};
          this.resInfo = { resPhone:'', resImgCode:''};
          this.resInfo2 = {resMesCode:'', respwd:'',};
          this.centerDialogVisible = true;
          this.checked = true;
          this.curindex = index;
          this.isregister = true;
          this.getKey() // 获取公钥
          if(index != 0){
            this.getimgCode()//获取验证码
          }
        },
        //退出
        exit(){
            let {token} = this.$loginInfo;
            Server.post(Path.logout,{
                token:token
            },res=>{
                localStorage.removeItem('studentkey');
                localStorage.removeItem('studentinfo');
                window.location.reload();
            })
        },

        /**
        * {
            "key": "前端加密的标识 前端生成后存储本地 发送给后端",
            "username": "登录 老师的用户名登录 学生手机号登录 机构手机号登录",
            "password": "密码",
            "type": "1 老师 2机构 3学生 4官方管理员登录",
            "source": "web/app",
            "organid": "普通机构老师登录必传"
            }
        */

        //登录
        submitForm: function (formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.login();//登录
                } else {
                    return false;
                }
            });
        },
        //注册
        submitForm2: function (formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.register2();//注册
                } else {
                    return false;
                }
            });
        },

        login(){
            var key = this.randomString(10);
            let {loginPhone,loginpwd} = this.loginInfo;
            
            //type : 1 老师 2机构 3学生 4官方管理员登录
            var postData = this.wrap_encrypt(JSON.stringify({
                username: loginPhone,
                password: loginpwd,
                source:'web',
                organtype:'',
                type:3,
                key:key,
                'prephone':this.isSelectValue,
            }));
            Server.post(Path.setlogin,
                {
                    data:postData
                }, (res) => {
                    let {code,data}=res;
                    if (res.code === 0) {
                        this.$message.success(res.info);
                        window.localStorage.setItem('studentkey',key);
                        window.localStorage.setItem('studentinfo',JSON.stringify(data));
                        var t = setTimeout(function (){
                            window.location.reload()
                        }, 1000);
                    }else{
                        this.$message.error(res.info);
                    }
                }
            );
        },

        //公钥加密
        wrap_encrypt(content) {
            var encryptobj = new JSEncrypt();
            encryptobj.setPublicKey(this.publickey);
            var result = "";
            var step = 117;
            var strlen = content.length;
            var count = Math.ceil(strlen / step);
            for (var i = 0; i < count; i++) {
                let chunk = content.substring(step * i, step * (1 + i));
                result += encryptobj.encrypt(chunk);
            }
            return result;
        },
        // 生成随机字符串
        randomString(len, charSet) {
            charSet = charSet || "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
            var randomString = "";
            for (var i = 0; i < len; i++) {
                var randomPoz = Math.floor(Math.random() * charSet.length);
                randomString += charSet.substring(randomPoz, randomPoz + 1);
            }
            return randomString;
        },
        //获取公钥
        getKey(){
            Server.post(Path.getPublicKey,{
			},res=>{
            let {code,data}=res;
                if(code===0){
                    this.publickey = res.key;
                }else{
                    this.$message.error(res.info);
                }
            })
        },

        //获取验证码
        getimgCode(){
            Server.post(Path.randomCode,
            {},
            res=>{
                let {code,data}=res;
				    if(code===0){
                        this.sessionid = data.sessionid
					    this.imgCode = data.codeimg
				    }else{
                        this.$message.error(res.info);
                    }
                }
            )
        },

        //注册
        register(formName){
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    if(this.checked==false){
                        // 请同意注册协议
                        this.$message.error(this.$t('Main.Login.registerText'));
                        return false;
                    };
                    // this.isregister = false
                    this.codeBoth2();//登录
                } else {
                    return false;
                }
            });
        },
        //发送手机验证码
        codeBoth2(){
          Server.post(Path.sendMobileMsg,{
            'mobile':this.resInfo.resPhone,
            'code':this.resInfo.resImgCode,
            'organid':1,
            'sessionid':this.sessionid,
            'type':2, //"type": "业务类型 1找回密码 2 注册",
            'prphone':this.isSelectValue,
            },res=>{
              let {code,data}=res;
              if(code===0){
                this.isregister = false
                this.getCode();
              }else{
                this.resInfo.resImgCode = "";
                this.$message.error(res.info);
                this.getimgCode();
              }
            }
          )
        },
        //60s后再发送
        getCode(){
            this.smsshow = false;
            this.getsms = true;
            const TIME_COUNT = 60;
            if (!this.timer) {
                this.count = TIME_COUNT;
                this.show = false;
                this.timer = setInterval(() => {
                if (this.count > 0 && this.count <= TIME_COUNT) {
                    this.count--;
                    } else {
                        this.show = true;
                        clearInterval(this.timer);
                        this.timer = null;
                        this.smsshow = true;
                        this.getsms = false;
                    }
                }, 1000)
            }
        },
        //提交注册
        register2(){
            var key = this.randomString(10);
            //type : 1 老师 2机构 3学生 4官方管理员登录
            let {resPhone} = this.resInfo;
            let {resMesCode,respwd} = this.resInfo2;
            var postData2 = this.wrap_encrypt(JSON.stringify({
                'organid':1,
                mobile:resPhone,
                code:resMesCode,
                password:respwd,
                prphone:this.isSelectValue,
                key:key
                // source:'web',
            }));
            Server.post(Path.register,{
                data:postData2
            },res=>{
            let {code,data}=res;
            if(code===0){
                this.$message.success(res.info);
                window.localStorage.setItem('studentkey',key);
                window.localStorage.setItem('studentinfo',JSON.stringify(data))
                window.localStorage.setItem('setFastShow',true);
                var t = setTimeout(function (){
                    window.location.reload()
                }, 1000);
            }else{
                this.resInfo2.resMesCode = '';
                this.resInfo2.respwd = '';
                this.$message.error(res.info);
            }
          })
        },
        //注册返回修改手机号
        back(){
            this.isregister = true;
            this.resInfo2.resMesCode = '';
            this.resInfo2.respwd = '';
            this.resInfo.resImgCode = '';
        },
        //获取二维码
        // getOriginImg(){
        //     Server.post(Path.getOriginImg,{
        //     },res=>{
        //         console.log(res)
        //         let {code,data}=res;
        //         if(code===0){
        //             this.formData=  data;
        //         }
        //     })
        // },
    },
    watch:{
		centerDialogVisible(val, oldVal){
			if(val == false){
                this.$refs.loginInfo.resetFields();
                // this.$refs.resInfo.resetFields();
                // this.$refs.resInfo2.resetFields();
			}
		}
	},
}
</script>
<style scoped lang="less">
@import "~@/assets/css/base";
.head-top{
    min-width: 1190px;
    line-height: 32px;
    background: #F4F4F4;
    font-size: 12px;
    .menu-list{
        margin-left: 16px;
    }
    .list{
        color: #666;
        position: relative;
        line-height: 32px;
        display: inline-block;
        padding: 0 15px 0 15px;
        float: left;
        cursor: pointer;
        &:hover{
            color:@color1;
        }
        &:after{
            content: "";
            width: 2px;
            height: 13px;
            background:  #999;
            position: absolute;
            z-index: 900;
            top: 10px;
            left: 0;
            border-radius: 4px;
        }
        &:last-child{
            padding-right:0; 
        }
    }
    .login-list{
        position: relative;
        li{
            color: #4d4d4d;
            position: relative;
            line-height: 32px;
            display: inline-block;
            padding: 0 18px 0 15px;
            cursor: pointer;
            &:hover{
                color:@color1;
            }
            &:last-child{
                padding: 0;
            }
            a{
                display: block;
            }
        }
        ul.register li{
            &:nth-child(1){
                color: @color5;
            }
            &:nth-child(2){
                color: #41B1E6;
            }
            &:hover{
                text-decoration: underline;
            }
        }
        ul.usertitle li{
            &:nth-child(1){
                color: @color5;
            }
            &:nth-child(2){
                color: #41B1E6;
            }
            &:hover{
                text-decoration: underline;
            }
        }
    }
    

}
.container{
    position: relative;
}
.banner-login{
    position: absolute;
    top:251px;
    right: 0;
    width:200px;
    height:328px;
    background:#fff;
    border-radius:6px;
    z-index: 999;
    text-align: center;
    .user-img{
        width:80px;
        height:80px;
        border: 1px solid  #E6E6E6;
        border-radius:50%; 
        margin: 32px auto 0px;
    }
    p.name{
        font-size: 12px;
        line-height: 44px;
        height: 44px;
        width: 80%;
        margin: 0 auto;
        color: @color1;
        overflow: hidden;
        text-align: center;
        text-overflow: ellipsis;
    }
    .btn{
        width:120px;
        height:32px;
        line-height: 32px;
        text-align: center;
        border-radius:4px;
        display: block;
        margin: 0 auto;
        border: 1px solid @color5;
        cursor: pointer;
    }
    .login-btn{
        color: @color5;
        margin-bottom: 22px;
        margin-top: 40px;
        &:hover{
            color: @color6;
        }
    }
    .res-btn{
        background: @color5;
        color: #fff;
        &:hover{
            background: @color6;
        }
    }
    .exit{
        color: #4D4D4D;
        font-size: 14px;
        cursor: pointer;
        &:hover{
            text-decoration: underline;
        }
    }
    .icon-btn{
        color: #4D4D4D;
        font-size: 14px;
        text-align: center;
        margin-top: 42px;
        .icon{
            width: 45%;
            display: inline-block;
            box-sizing: border-box;
            text-align: center;
            em{
                display: block;
                width: 27px;
                height: 27px;
                margin: 0 auto 8px;

            }
            &.icon1{
                em{
                    background: url('./assets/images/icon1.png') no-repeat center;
                    background-size:100%; 
                }
            }
            &.icon2{
                em{
                    background: url('./assets/images/icon2.png') no-repeat center;
                    background-size:100%; 
                }
            }
        }
    }
}
 </style>



