<template>
  <div class=''>
    <Head2 isTitleShow = 'true' title ='下载' isHotLine = 'true' style='border-bottom:2px solid #3F9CE8;'></Head2>
    <div class="container bg-white outBox" >
      <ul>
        <li class="lemon">
          <div class="leftBlock">
            <div class="leftImg"><img src="./assets/lemondownload@2x.png" alt=""></div>
            <div class="leftText">柠檬树下大语文</div>
          </div>
          <div class="rightBlock">
            <div class="outLink" v-for="(item,index) in lemonCodeurl" :key="index">
              <div class="codeImg"><img :src="item.url" alt=""></div>
              <div class="codeBox">
                <div class="iconImg"><img :src="item.iconUrl" alt=""></div>
                <div class="iconText">{{item.btnText}}</div>
              </div>
            </div>
          </div>
        </li>
        <li class="other" v-for="(item,index) in listArr" :key="index">
          <div class="leftBlock">
            <div class="leftImg"><img :src="item.url" alt=""></div>
            <div class="leftText">{{item.text}}</div>
          </div>
          <div class="rightBlock">
            <div class="linkText" v-if="index!=3" @click="openLink(item.linkArr[0])">Windows  系统下载</div>
            <div class="linkText" v-else @click="openLink(item.linkArr[0])">win xp  系统下载</div>
            <div class="linkText" v-if="index!=3" @click="openLink(item.linkArr[1])">mac  系统下载</div>
          </div>
        </li>
      </ul>
    </div>
    <Feet></Feet>
  </div>
</template>
<script>
import Server from "@/service/request";
import Path from "@/service/Path";
import Head2 from '@/component/header2';
import Feet from '@/component/feet';
import android from './assets/android@2x.png';
import IOS from './assets/IOS@2x.png';
import talkcloud from './assets/talkcloud@2x.png';
import teamview from './assets/teamview@2x.png';
import Chrome from './assets/Chrome@2x.png';
import speed from './assets/speed@2x.png';
export default {
  components:{
    Head2,
    Feet,
  },
  data(){
    return{
      lemonCodeurl:[
        {
          url:talkcloud,
          iconUrl:android,
          btnText:'Android 下载'
        },
        {
          url:talkcloud,
          iconUrl:IOS,
          btnText:'ios 下载'
        }
      ],
      listArr:[
        {
          url:talkcloud,
          text:'CloudClass 云课堂插件',
          windows:'cloudclasswindows',
          mac:'cloudclassmac',
          linkArr:[]
        },
        {
          url:teamview,
          text:'TeamView 远程协助软件',
          windows:'teamviewwindows',
          mac:'teamviewmac',
          linkArr:[]
        },
        {
          url:Chrome,
          text:'Chrome 浏览器',
          windows:'chromewindows',
          mac:'chromemac',
          linkArr:[]
        },
        {
          url:speed,
          text:'360 极速浏览器',
          windows:'browser360xp',
          mac:'',
          linkArr:[]
        },
      ] 
    }
  },
  created() {
    this.geturl()
  },
  methods:{
    geturl(){
      Server.post(
				Path.configureShow, {

				},
				res => {
					let { code, data } = res;
					if(code === 0) { 
            this.lemonCodeurl[0].url = data.qrcodeandroid
            this.lemonCodeurl[1].url = data.qrcodeios
            this.listArr.forEach((item,index)=>{
              var windowsflag = item.windows
              var macflag = item.mac
              item.linkArr.push(data[windowsflag])
              item.linkArr.push(data[macflag]||'')
            })
					}else{
            this.$Message.error(res.info)
          }
				}
			);
    },
    openLink(url){
      // console.log(url)
      window.open(url)
    }
  },
}
</script>
<style scoped lang="less">
@import "~@/assets/css/base";
.outBox{
  div:after, ul:after, dl:after, .clearfix:after {
    content: none;
    display: block;
  }
  padding: 12px 22px 32px;
  div{
    display: inline-block;
  }
  li{
    border-bottom: 1px solid #EDEDED;
    padding-left: 66px;
    &:last-of-type{
      border-bottom: none;
    }
  }
  .lemon{
    height: 266px;
    line-height: 266px;
  }
  .other{
    height: 168px;
    line-height: 168px;
    .leftBlock{
      position: relative;
      top: 24px;
    }
  }
  .leftBlock{
    margin-right: 80px;
    min-width: 340px;
    .leftImg{
      width: 69px;
      height: 69px;
      overflow: hidden;
      position: relative;
      margin-right: 42px;
      img{
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
      }
    }
    .leftText{
      position: relative;
      top: -28px;
      color: #1A1A1A;
      font-size: 20px;
    }
  }
  .rightBlock{
    .outLink{
      position: relative;
      min-width: 201px;
      text-align: center;
      margin-right: 40px;
      .codeImg{
        width: 118px;
        height: 118px;
        overflow: hidden;
        position: relative;
        img{
          width: 100%;
          height: 100%;
          position: absolute;
          left: 0;
          top: 0;
        }
      }
      .codeBox{
        width: 201px;
        height: 41px;
        border-radius: 10px;
        background-color: #3F9CE8;
        color: white;
        font-size: 20px;
        position: absolute;
        left: 0;
        bottom: 60px;
        .iconImg{
          width: 18px;
          height: 21px;
          overflow: hidden;
          position: relative;
          top: -110px;
          margin-right: 13px;
          img{
            width: 100%;
            height: 100%;
            position: absolute;
            left: 0;
            top: 0;
          }
        }
        .iconText{
          height: 41px;
          position: relative;
          top: -114px;
        }
      }
    }
    .linkText{
      width: 201px;
      height: 59px;
      line-height: 59px;
      font-size: 20px;
      text-align: center;
      color: white;
      background-color: #3F9CE8;
      border-radius: 10px;
      margin-right: 40px;
      cursor: pointer;
    }
  }
}
</style>




