<template>
  <div class="wrapper">
    <Head2 :isCur='0' isNavShow = true isSeachShow = true isHotLine = 'true' isBannerLogin = 'true' ref="child"></Head2>
    <section>
      <!--轮播-->
      <banner></banner>
      <!--柠檬课堂-->
      <hot />
      <setion1></setion1>
      <setion2></setion2>
      <setion3></setion3>
      <setion4></setion4>
      <setion5></setion5>
      <setion7></setion7>
      <setion6></setion6>
    </section>
    <Feet></Feet>
    <v-backTop />
  </div>
</template>
<script>
import Server from "@/service/request";
import Path from "@/service/Path";
import Feet from "@/component/feet";
import Head2 from '@/component/header2'
import hot from './hot';
import banner from './banner';
import backTop from "@/component/backTop";
import setion1 from './setion1';
import setion2 from './setion2';
import setion3 from './setion3';
import setion4 from './setion4';
import setion5 from './setion5';
import setion6 from './setion6';
import setion7 from './setion7';

export default {
  data() {
    return {
      CourseList:[], //课程分类
      num:0,
    };
  },
  components: {
    Feet,
    Head2,
    hot,
    banner,
    setion1,
    setion2,
    setion3,
    setion4,
    setion5,
    setion6,
    setion7,
    'v-backTop':backTop,
  },
  created() {
    //获取首页课程分类
    // this.getChildOrList();
    this.getTalk();
  },
  methods: {
    //获取首页课程分类
    getChildOrList(){
      Server.post(Path.getChildOrList,{
        organid:1,
      },res=>{
        let {code,data}=res;
        if(code===0){
          this.CourseList = [...data];
          this.CourseList.map((e,i) => {
            if(e.child.length > 0){
              this.num ++;
            }
          })
        }
      })
    },
    // 在线聊天弹框
    getTalk(){
      var _53code = document.createElement("script");
      _53code.src = "https://tb.53kf.com/code/code/10184557/1";
      var s = document.getElementsByTagName("script")[0]; 
      s.parentNode.insertBefore(_53code, s);
    },
  }
};
</script>
<style scoped lang="less">
section {
  min-width: 1190px;
  width: 100%;
  height: auto;
}
</style>



