<template>
  <div id="allmap" class="allmap"></div>
</template>
<script>
import BMap from 'BMap'
  export default {
    data() {
      return {
        address_detail: null,
        center: {lng: 116.40387397, lat: 39.91488908}
      }
    },
    mounted() {
      this.ready();
    },
    methods: {
      ready: function() {
        //创建Map实例
        var map = new BMap.Map("allmap");
        // 初始化地图,设置中心点坐标
        var point = new BMap.Point(106.568054,29.535047);// 创建点坐标
        map.centerAndZoom(point,15);
        //添加鼠标滚动缩放
        map.enableScrollWheelZoom();

        //添加缩略图控件
        map.addControl(new BMap.OverviewMapControl({isOpen:false,anchor:BMAP_ANCHOR_BOTTOM_RIGHT}));
        //添加缩放平移控件
        map.addControl(new BMap.NavigationControl());
        //添加比例尺控件
        map.addControl(new BMap.ScaleControl());
        //添加地图类型控件
        //map.addControl(new BMap.MapTypeControl());

        //设置标注的图标
        var icon = new BMap.Icon("http://api0.map.bdimg.com/images/marker_red_sprite.png",new BMap.Size(39,25));
        //设置标注的经纬度
        var marker = new BMap.Marker(new BMap.Point(106.568054,29.535047),{icon:icon});
        //把标注添加到地图上
        map.addOverlay(marker);

        marker.addEventListener("click",function(){
          this.openInfoWindow(infowindow);
        });
      }
    }
  }
</script>
<style lang="less" scoped>
.bm-view,.allmap{
  width: 1000px;
  height: 324px;
  margin: 10px auto 30px;
}
</style>


