<!--频道-->
<template>
    <!--  @mouseleave="showuserlist = false" -->
    <div class='nav-wrapper' @mouseleave="showTypeList = false">
        <div class='container'>
            <ul class='nav-list'>
                <li class='list' :class="isCur==0?'cur':''"  @mouseover="showTypeList = true">
                    <router-link to="home">
                        <img src='./assets/icon.png' />
                        课程分类
                    </router-link>
                </li>
                <li class='list' :class="isCur==1?'cur':''">
                    <router-link :to="{path:'classpage',query: {type:1}}">免费学</router-link>
                </li>
                <li class='list' :class="isCur==2?'cur':''">
                    <router-link :to="{path:'classpage',query: {type:0}}">课程中心</router-link>
                </li>
                <li class='list' :class="isCur==3?'cur':''">
                    <router-link to="teacher">名师堂</router-link>
                </li>
               <!--  :class="index==4?cur:''" -->
               <li class='list' :class="isCur==4?'cur':''">
                    <router-link to="combo">课时套餐</router-link>
                </li> 
            </ul>
            <!-- @mouseleave="showTypeList = false"  v-if='showTypeList && isCur == 0'-->
            <div>
                <typeitem v-if='showTypeList && isCur == 0' ></typeitem>
            </div>
            <div class="download" @click="$router.push('/download')">
                <img src="./assets/download@2x.png" alt="">
                <div>客户端下载</div>
            </div>
        </div>
    </div>
</template>

<script>
import typeitem from '../header2/typeitem/index'
export default {
    props: ['isCur'],
    data(){
        return {
            showTypeList:false,
        }
    },
    components:{
        typeitem,
    },
}
</script>
<style scoped lang="less">
@import "~@/assets/css/base";
.nav-wrapper{
    display: block;
    background: #fff;
    border-bottom: 2px solid @color1;
    min-width: 1190px;
    .container{
        position: relative;
        .download{
            position: absolute;
            top: 10px;
            right: 50px;
            cursor: pointer;
            img{
                width: 21px;
                height: 19px;
                overflow: hidden;
                vertical-align: middle;
                margin-right: 10px;
            }
            div{
                display: inline-block;
                color: #666666;
                font-size: 12px;
            }
        }
    }
}
.nav-list{
    li{
        line-height: 38px;
        font-size: 16px;
        color: #000;
        display: inline-block;
        text-align: center;
        cursor: pointer;
        a{
            display: block;
            padding: 0 68px;

        }
        &.cur{
            color: #fff;
            background: #41B1E6;
            border-radius:2px 2px 0px 0px;
            a{
                color: #fff;
            }
        }
        &:first-child{
            width: 200px;
            padding: 0;
            text-align: left;
            a{
                padding: 0;
                img{
                    display: inline-block;
                    width: 16px;
                    height: 14px;
                    vertical-align: top;
                    margin: 13px 15px 0 38px;
                }
            }
        }
    }
}
 </style>



