            this.newCountries2 = JSON.parse(JSON.stringify(this.newCountries));
            let abc = this.newCountries2[1].options;
            var compare = function (obj1, obj2) {
                var val1 = obj1.en;
                var val2 = obj2.en;
                if (val1 < val2) {
                    return -1;
                } else if (val1 > val2) {
                    return 1;
                } else {
                    return 0;
                }            
            } 
            abc = abc.sort(compare)
        }
