<template>
  <div class="">
    <Head2  isSeachShow = true isHotLine = 'true' style='border-bottom:2px solid #3F9CE8;' :keywords1='keywords' :searchtype1='searchtype'></Head2>
    <section class="container">
      <!--列表页数据-->
      <div class="mb-30" :style="{minHeight: fullHeight-82-76-315+'px'}">
        <div class="classtitle" v-if = 'searchtype == 1'>
          共有{{total}}门包含'<span>{{keywords}}</span>'的课程
        </div>
        <div class="classtitle" v-if = 'searchtype == 2'>
          共有{{total}}个包含'<span>{{keywords}}</span>'的老师
        </div>
        <!-- 课程列表 -->
        <courseList1 :filterCourserList='filterCourserList.arr' :searchtypenum='1'  v-if = 'searchtype == 1' class='bg-white'></courseList1>
        <!-- 老师列表 -->
        <courseList1 :filterCourserList='filterCourserList.arr' :searchtypenum='2'  v-if = 'searchtype == 2' class='bg-white'></courseList1>
      </div>
      <!--分页-->
      <Page :total="total" :current-page='current' :limit="limit" @pagechange="pagechange" v-if = 'total!= 0'></Page>
    </section>
    <Feet></Feet>
    <v-backTop />
  </div>
</template>
<script>
import Head2 from "@/component/header2";
import Server from "@/service/request";
import Path from "@/service/Path";
import Page from "@/component/page";
import Feet from "@/component/feet";
import backTop from "@/component/backTop";
import courseList1 from "@/component/courselist1";
export default {
  data() {
    return {
      searchtype: '' ,//1:课程搜索2:机构搜索
      keywords:'',// 搜索关键字
      filterCourserList: {
        arr:[],
      }, //列表数据
      //分页
      total: 0, //总条数
      limit: 10, //每页显示条数
      current: 1, //当前页数
      isShowDetails:true,
      fullHeight:0,
    };
  },
  components: {
    Head2,
    Page,
    Feet,
    courseList1,
    'v-backTop':backTop,
  },
  watch:{
    $route(to,from) {
      this.searchtype = this.$route.query.type;
      this.keywords = this.$route.query.k;
      this.searchCourseOrTeacher();
    }  
  },
  mounted() {
    this.fullHeight = document.body.clientHeight;
  },
  created() {
    this.searchtype = this.$route.query.type;
    this.keywords = this.$route.query.k;
    this.searchCourseOrTeacher();
  },
  methods: {
    //分页
    pagechange(currentPage) {
      this.searchCourseOrTeacher(currentPage);
    },
    //课程课表 数据
    searchCourseOrTeacher(currentPage=1) {
      let { activeTabId, activeCateId } = this;
      Server.post(
        /* searchtype:1课程搜索2老师搜索,*/
        Path.searchCourseOrTeacher,
        {
          'searchtype':this.searchtype, 
          'search':this.keywords,
           pagenum:currentPage,
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
            this.total = data.pageinfo.total;
            this.current = Number(data.pageinfo.pagenum);
            this.limit = Number(data.pageinfo.pagesize);
            this.filterCourserList = data;
            if(this.filterCourserList.arr.length > 0){
              this.isShowDetails = true;
            }else{
              this.isShowDetails = false;
            }
          }
        }
      );
    },
  }
};
</script>
<style scoped lang="less">
@import "~@/assets/css/base";
.classtitle {
  line-height: 54px;
  padding-left: 30px;
  font-size: 14px;
  color: #37383D;
  span{
    color: @color1;
  }
}
</style>



