import Vue from "vue";
import Vuex from "vuex";
import Server from '@/service/request';
import Path from '@/service/Path';

Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    isFastShow:false,
  },
  mutations: {
    setFastShow(state,f){
      state.isFastShow = f;
    },
  },
});

export default {
  store
};



