<template>
  <!--领取试听课弹窗-->
  <el-dialog
    :visible.sync="centerDialogVisible"
    width="390px"
    :lock-scroll='false'
    center>
    <div class="loginregister" >
      <div class="info">
        <div class="registers">
          <div class="topTitle">领取试听课</div>
          <el-form  :model="resInfo" :rules="resInfoRules" ref="resInfo" key='1'>
             <el-form-item prop="resName" class='res-code'>
              <el-input v-model="resInfo.resName" class='code-input' placeholder="请输入昵称" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item  prop="resPhone" class='h-no-border-area'>
              <v-areaCode @selectValue = 'selectValue'></v-areaCode>
              <!-- 请输手机号 -->
              <el-input v-model="resInfo.resPhone" :placeholder="$t('Main.Login.loginPhone')" class='pdleft55' autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item prop="resImgCode" class='res-code' style='position:relative'>
              <!-- 请输入验证码 -->
              <el-input v-model="resInfo.resImgCode" class='code-input' :placeholder="$t('Main.Login.codeinput')" autocomplete="off"></el-input>
              <img  :src="imgCode" alt="" @click="getimgCode()" class='code-img'/>
            </el-form-item>
            <el-form-item  prop="resMesCode" class='mes-code'>
              <!-- 请输入短信验证码 -->
              <el-input v-model="resInfo.resMesCode" :placeholder="$t('Main.Login.resMesCode')" autocomplete="off"></el-input>
              <!-- 秒后重新获取 -->
              <p class='time1' v-if="getsms">
                  {{count}}{{$t('Main.Login.getsms')}}
              </p>
              <!-- 获取短信验证码 -->
              <p  class='time1' v-else @click="sendUpdateMobileMsg()">{{$t('Main.Login.codeBoth')}}</p>
            </el-form-item>
            <el-button type="primary" @click="submitForm('resInfo')" class='login-btn'>立即领取</el-button>
          </el-form>
        </div>
      </div>
    </div>
  </el-dialog>
</template>
<script>
import Server from "@/service/request";
import Path from "@/service/Path";
import areaCode from '@/component/areaCode'
export default {
  name:"",
  data(){
    var validatePhone = (rule, value, callback) => {
      var MobileRegex = /^1\d{5,14}$/;
      if (!MobileRegex.test(value)) {
        // 请输入正确的11位手机号码
        callback(new Error(this.$t('Main.Login.MobileRegex')))
      } else {
        callback();
      }
    };
    var validateMes = (rule, value, callback) => {
      var MesRegex = /^(\d){6}$/;
      if (!MesRegex.test(value)) {
        // 请输入正确的6位验证码
        callback(new Error(this.$t('Main.Login.MesRegex')))
      } else {
        callback();
      }
    };
    var validateCode = (rule, value, callback) => {
      var ImgRegex = /^[A-Za-z0-9]{5}$/;
      if (!ImgRegex.test(value)) {
        // 请输入正确的5位验证码
        callback(new Error(this.$t('Main.Login.ImgRegex')))
      } else {
        callback();
      }
    };
    return{
      centerDialogVisible: false ,// 模态框
      isSelectValue: '86', //获取区号
      resInfo:{
        resName:'',//昵称
        resPhone:'',//手机号
        resImgCode:'',//图片验证码
        resMesCode:'',//短信验证码
      },
      imgCode:null, //图形验证码图片
      sessionid:'',//图形验证码id

      getsms:false,
      smsshow:false,
      count:"",//60s计时器
      
      resInfoRules: {
        resName: [
          // 请输入验证码
          {required: true, message: '请输入昵称', trigger: 'blur'},
        ],
        resImgCode: [
          // 请输入验证码
          {required: true, message: this.$t('Main.Login.codeinput'), trigger: 'blur'},
          // { validator: validateCode, trigger: 'blur' }
        ],
        resPhone: [
          // 请输入手机号码
          {required: true, message: this.$t('Main.Login.loginPhone'),  trigger: 'blur'},
          { validator: validatePhone, trigger: 'blur' }
        ],
        resMesCode: [
          // 请输入短信验证码
          {required: true, message: this.$t('Main.Login.resMesCode'), trigger: 'blur' },
          { validator: validateMes, trigger: 'blur' }
        ],
      },
    }
  },
  components:{
    'v-areaCode':areaCode,
  },
  created(){
    this.getimgCode() // 获取图形验证码
  },
  methods:{ 
    childFn(){
      this.centerDialogVisible = true;
    },
    //获取区号
    selectValue(val){
      this.isSelectValue = val;
    },
    //提交表单
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.addReceiveInfo();//免费领取
        }else {
          return false;
        }
      });
    },
    //获取图形验证码
    getimgCode(){
      Server.post(Path.randomCode,
      {},
      res=>{
        let {code,data}=res;
        if(code===0){
          this.sessionid = data.sessionid
          this.imgCode = data.codeimg
        }else{
          this.$message.error(res.info);
        }
      })
    },
    //发送手机验证码
    sendUpdateMobileMsg(){
      if(this.resInfo.resPhone == ''){
        this.$message.error(this.$t('Main.Login.loginPhone'));
        return;
      }
      Server.post(Path.sendUpdateMobileMsg,{
        'mobile':this.resInfo.resPhone,
        'prphone':this.isSelectValue,
        },res=>{
          let {code,data}=res;
          if(code===0){
            this.getCode();
          }else{
            this.resInfo.resImgCode = "";
            this.$message.error(res.info);
            this.getimgCode();
          }
        }
      )
    },
    //60s后再发送
    getCode(){
      this.smsshow = false;
      this.getsms = true;
      const TIME_COUNT = 60;
      if (!this.timer) {
        this.count = TIME_COUNT;
        this.show = false;
        this.timer = setInterval(() => {
          if (this.count > 0 && this.count <= TIME_COUNT) {
            this.count--;
          } else {
            this.show = true;
            clearInterval(this.timer);
            this.timer = null;
            this.smsshow = true;
            this.getsms = false;
          }
        }, 1000)
      }
    },
    //领取免费
    addReceiveInfo(){
      let {resName,resPhone,resMesCode} = this.resInfo;
      var postData = {
        name:resName,
        mobile:resPhone,
        code:resMesCode,
        prphone:this.isSelectValue,
      };
      Server.post(Path.addReceiveInfo,{
        ...postData
      },res=>{
      let {code,data}=res;
      if(code===0){
        this.$message.success(res.info);
        this.centerDialogVisible = false;
      }else{
        this.resInfo.resMesCode = '';
        this.$message.error(res.info);
      }
      })
    },
  },
  watch:{
		centerDialogVisible(val, oldVal){
			if(val == false){
        this.$refs.resInfo.resetFields();
			}
		}
	},
}
</script>
<style scoped lang="less">
 </style>



