<!--页脚-->
<template>
    <div>
        <top ref="child" :isBannerLogin=isBannerLogin></top>
        <div class = 'header'>
            <div class="container">
                <div class='left'>
                    <router-link to = '/' class='titlebar'>
                        <img :src="logo" alt="" />
                    </router-link>
                    <p v-if='isTitleShow'>{{title}}</p>
                </div>
                <div class='right' v-if='isStepShow'>
                    <Step :step=isStepNum :desc="['填写并确认订单','支付订单','订单完成']" />
                </div>
                <div class='hotLine' v-if='isHotLine'>
                    <em></em>
                    <div>
                        <p>客服热线</p>
                        <p>023-6253-5196</p>
                    </div>
                </div>
                <div class="seach-box" v-if='isSeachShow'>
                    <el-select v-model="searchtype" placeholder="">
                        <el-option
                        v-for="item in options"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                        </el-option>
                    </el-select>
                    <!-- 请输入关键字 -->
                    <input class = 'seach-input' type="text" placeholder="输入你想要的课程或者老师"  v-model.trim="keywords" @keyup.enter="searchPage" onkeyup="value=value.replace(/[^\w\u4E00-\u9FA5]/g,'')"/>
                    <div class='seach-btn' @click = 'searchPage()'>
                        <i class="el-icon-search search"  ></i>
                    </div>
                </div>
            </div>
        </div>
        <Nav :isCur = isCur v-if='isNavShow'></Nav>
    </div>
</template>

<script>
import logo from './assets/images/logo.png'
import Step from "@/component/step";
import Nav from "@/component/nav";
import Top from '@/component/top'
export default {
    name:"",
    props: ['isStepShow','isStepNum','isTitleShow','title','isCur','isSeachShow','isNavShow','isHotLine','isBannerLogin','keywords1','searchtype1'],
    data(){
        return{
            logo,
            options: [//搜索框选项
                { value: '1', label: this.$t('Main.Header.course') }, //课程
                { value: '2', label: '老师' } //机构
            ],
            searchtype: this.searchtype1 || '1',//搜索类型
            keywords:this.keywords1 || '',// 搜索关键字
        }
    },
    components:{
        Step,
        Top,
        Nav,
    },
    created(){
    },
    methods:{ 
        //登录
        goLogin(index){
            this.$refs.child.changeactive(index);
        },
        // 
        //跳转搜索页面
        searchPage(){
            let keywords = this.keywords;
            var pattern = new RegExp("[`~!@#$^&*()=|{}':;',\\[\\].<>/?~！@#￥……&*（）&;— - _ ——|{}【】‘；：”“'。，、？ ]")
            var rs = "";
            for (var i = 0; i < keywords.length; i++) {
                rs = rs + keywords.substr(i, 1).replace(pattern, '');
            }
            keywords = rs;
            if(keywords == ''){
                this.$message.error(this.$t('Main.Header.SearchText2')) //请输入关键字
                return;
            }
            this.$router.push({
                path:"/Searchpage",
                query:{
                    'k':keywords,
                    'type':this.searchtype,
                }
            })
        },
    },
}
</script>
<style scoped lang="less">
@import "~@/assets/css/base";
.header{
    height: 120px;
    background: #fff;
    overflow: hidden;
    min-width: 1190px;
    .container{
        position: relative;
    }
    .left{
        .titlebar{
            display: block;
            width: 230px;
            height: 120px;
            line-height: 120px;
            float: left;
            img{
                vertical-align: middle;
            }
        }
        p{
            float: left;
            display: block;
            color: @color1;
            font-size:22px ;
            line-height: 49px;
            padding-left: 32px;
            margin: 41px 0 30px;
            border-left:2px solid @color1; 
        }
    }
    .right{
        position: relative;
        width: 340px;
        margin-top: 28px;
    }
    //客服热线
    .hotLine{
        float: right;
        width: 230px;
        margin-top: 36px;
        em{
            width:38px;
            height:38px;
            display: inline-block;
            vertical-align: top;
            background: url('./assets/images/tel_icon.png') center no-repeat;
        }
        div{
            border-left:1px solid #999;
            height:38px;
            padding-left: 14px;
            margin-left: 15px;
            display: inline-block;
            p:nth-child(1){
                color: #808080;
                font-size: 12px;
                line-height: 12px;
                margin-bottom: 10px;
            }
            p:nth-child(2){
                color: #666;
                font-size: 16px;
                line-height: 16px;
            }
        }
    }
    //搜索框
    .seach-box{
        width: 395px;
        border-radius: 5px;
        position: absolute;
        right: 450px;
        top: 36px;
        background: #fff;
        border: 2px solid @color5;
        .seach-input{
            float: left;
            width: 273px;
            height: 40px;
            border: none;
            outline: none;
            background: #fff;
        }
        .seach-btn{
            cursor: pointer;
            width: 46px;
            height: 40px;
            line-height: 40px;
            text-align: center;
            float: right;
            background: @color5;
            .search{
                width: 18px;
                height: 18px;
                margin: 11px auto;
                display: block;
                font-size: 18px;
                color: #fff;
            }
        }
    }
    
}
</style>



