<template>
  <div>
		<!-- 收货地址 -->
		<div class="addressList">
			<div class="couponBox">
				<p class="orderTitle left">收货地址</p>
			</div>
			<ul>
				<li 
				 v-for = '(item,i) in addressList' :key='i'>
					<p class="brushVoucher left">
						<el-radio v-model="addressRadio" :label="i" @change="getAddress(item.id)">
							<span class='item'>{{item.pname}}省{{item.cname}}{{item.aname}} </span>
							<span class='item itemaddress '>{{item.address}}</span> 
							<span class='item itemlinkman '>{{item.linkman}}</span>
							<span class='item'>{{item.mobile}}</span></el-radio>
					</p>
					<p class="edit-btn right" @click="editAddress(item)">编辑</p>
					<p class="default-icon right" v-if='item.isdefault == 1'>默认地址</p>
				</li>
			</ul>
			<div class='add-btn' @click="addAddress()">+添加地址</div>
		</div>

		<!--收货地址-->
		<el-dialog
			:visible.sync="centerDialogVisible"
			width="550px"
			:lock-scroll='false'
			center>
			<div class="address-wrapper" >
				<div class="topTitle">请完善收货地址</div>
					<el-form label-width="100px" :model="formInfo" ref="formInfo" :rules="infoValidate" class="formInfo">
						<el-form-item label="选择地区" required>
							<el-col :span="7">
								<el-form-item label="" prop="pid">
									<el-select v-model="formInfo.pid" @change="getCity(formInfo.pid)" placeholder="请选择省">
										<el-option :value="item.id" v-for="item in provinceList" :key="item.id" :label='item.name'></el-option>
									</el-select>
								</el-form-item>
							</el-col>
							<el-col class="line tc" :span="1">-</el-col>
							<el-col :span="7">
								<el-form-item label="" prop="cityid">
									<el-select v-model="formInfo.cityid" @change="getArea(formInfo.cityid)" placeholder="请选择市">
										<el-option :value="item.id" v-for="item in cityList" :key="item.id" :label='item.name'></el-option>
									</el-select>
								</el-form-item>
							</el-col>
							<el-col class="line tc" :span="1">-</el-col>
							<el-col :span="7">
								<el-form-item label="" prop="areaid">
									<el-select v-model="formInfo.areaid" placeholder="请选择区">
										<el-option :value="item.id" v-for="item in areaList" :key="item.id" :label='item.name'></el-option>
									</el-select>
								</el-form-item>
							</el-col>
						</el-form-item>
						<el-form-item label="详细地址" prop="address">
							<el-col :span="23">
								<el-input type="textarea" resize='none' placeholder="请输入详细地址信息，如道路、门牌号、小区、楼栋号、单元等信息,长度不超过50个字符" v-model="formInfo.address" :maxlength='50'></el-input>
							</el-col>
						</el-form-item>
						<el-form-item label="邮政编码" prop="zipcode">
							<el-col :span="23">
								<el-input placeholder="如您不清楚邮递区号，请填写000000" v-model="formInfo.zipcode"></el-input>
							</el-col>
						</el-form-item>
						<el-form-item label="收货人姓名" prop="linkman">
							<el-col :span="23">
								<el-input placeholder="长度不超过25个字符" v-model="formInfo.linkman" :maxlength='25'></el-input>
							</el-col>
						</el-form-item>
						<el-form-item label="联系方式"  prop="mobile">
							<el-col :span="23">
								<el-input placeholder="请填写6~20位电话或手机号码" v-model="formInfo.mobile"></el-input>
							</el-col>
						</el-form-item>
						<el-form-item label="">
							<el-checkbox-group v-model="single">
								<el-checkbox label="设置为默认收货地址" name="type"></el-checkbox>
							</el-checkbox-group>
						</el-form-item>
						<el-form-item>
							<el-button type="primary" @click="submitForm('formInfo')">保存</el-button>
							<el-button @click="resetForm('formInfo')">取消</el-button>
						</el-form-item>
					</el-form>
			</div>
		</el-dialog>

  </div>
</template>
<script>
import Server from "@/service/request";
import Path from "@/service/Path";
export default { 
	data(){
		return{
			addressList:[],//收货地址列表
			addressRadio:0,
			// 地址省市区
      provinceList: [],
      cityList: [],
			areaList: [],
			
			centerDialogVisible:false,
			single:false,
			formInfo: {
				pid: "",
				cityid: "",
				areaid: "",
				zipcode: "",
				linkman: "",
				mobile: "",
				area: "",
			},
			infoValidate: {
				pid: [
					{ required: true, message: "请选择省", trigger: "blur", type: "number" }
				],
				cityid: [
					{ required: true, message: "请选择市",trigger: "blur",type: "number" }
				],
				areaid: [
					// { required: true, message: "请选择区/县",trigger: "blur",type: "number" }
				],
				address: [
					{ required: true, message: "请输入详细地址", trigger: "blur" }
				],
				zipcode: [
					{ required: true, message: "请输入邮政编码", trigger: "blur" },
					{ type: "string", message: "请输入正确的邮政编码", trigger: "blur", pattern: /^[0-9]{6}$/ }
				],
				linkman: [
					{ required: true, message: "请输入收货人姓名", trigger: "blur" },
					// { type: "string", min: 1, max: 25, message: "长度不超过25个字符", trigger: "blur" }
				],
				mobile: [
					{ required: true, message: "请输入联系方式", trigger: "blur" },
					{ type: "string", min: 6, max: 20, message: "请填写6~20位电话或手机号码", trigger: "blur", pattern: /^[0-9\-]*$/ }
				]
			}
		}
	},
	mounted(){
		//获取收货地址
		this.getAddressList();
	},
	watch:{
		centerDialogVisible(val, oldVal){
			if(val == false){
				this.cityList = [];
				this.areaList = [];
				this.$refs.formInfo.resetFields()
			}
		}
	},
	methods:{
		submitForm(formName) {
			this.$refs[formName].validate((valid) => {
				if (valid) {
					this.addOrUpdateAddress();
				} else {
					// console.log('error submit!!');
					return false;
				}
			});
		},
		resetForm(formName) {
			this.$refs[formName].resetFields();
			this.centerDialogVisible = false;
		},
		// 获取当前地址id
		getAddress(val){
			this.$emit('getAddressId',val);
		},
		// 编辑地址弹框
		editAddress(val){
			// console.log(JSON.parse(JSON.stringify(val)))
			this.formInfo = JSON.parse(JSON.stringify(val))
			this.formInfo.areaid = Number(this.formInfo.areaid)
			this.single = Boolean(val.isdefault)
			this.getProvince();
			this.getCity(val.pid);
			this.getArea(val.cityid);
			this.centerDialogVisible = true;
		},
		// 添加地址弹框
		addAddress(){
			this.single = false;
			this.formInfo = {};
			this.getProvince();
			this.centerDialogVisible  =  true;
		},
		// 查询收货地址
		getAddressList() {
			Server.post(
				Path.getAddressList, {
				},
				res => {
					let {
						code, data
					} = res;
					if(code === 0) {
						this.addressList = data; 
						// this.ruleForm = data[0];
						data.map((el,i) => {
							if(el.isdefault == 1){
								this.addressRadio = i;
								this.$emit('getAddressId',el.id);
								return;
							}else{
								this.addressRadio = 0;
								this.$emit('getAddressId',data[0].id);
								return;
							}
						})
					}
				}
			);
		},
		// 提交表单
    addOrUpdateAddress() {
      Server.post(
				Path.addOrUpdateAddress,
				{
					pid: this.formInfo.pid,
					cityid: this.formInfo.cityid,
					areaid: this.formInfo.areaid,
					address: this.formInfo.address,
					linkman: this.formInfo.linkman,
					mobile: this.formInfo.mobile,
					zipcode: this.formInfo.zipcode,
					isdefault: Number(this.single),
					id: this.formInfo.id || ''
				},
				res => {
					let { code, data } = res;
					if (code === 0) {
						this.$message.success(res.info);
						this.centerDialogVisible  =  false;
						this.getAddressList();
					} else {
						this.$message.error(res.info);
					}
				}
			);
			this.isShowCloseModal = false;
		},
		// 查询省
    getProvince() {
      Server.post(
        Path.getCityList,
        {
          parentid: 7
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
						this.provinceList = data;
          } else {
            this.$message.error(res.info);
          }
        }
      );
    },
    // 查询市
    getCity(findArea) {
      this.cityList = [];
      this.areaList = [];
      this.cityId = 0;
      this.areaId = 0;
      Server.post(
        Path.getCityList,
        {
          parentid: findArea
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
            if (data.length == 0) {
              //如果选择完省市级列表返回为空数组，那么清空areaList
              this.areaList = [];
              this.areaId = 0;
            }
            this.cityList = data;
          } else {
            this.$message.error(res.info);
          }
        }
      );
    },
    // 查询区/县
    getArea(findCity) {
      Server.post(
        Path.getCityList,
        {
          parentid: findCity
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
            this.areaList = data;
          } else {
            this.$message.error(res.info);
          }
        }
      );
    },
	},  
}
</script>
<style lang="less" scoped>
	@import '../assets/css/placeOrder.less';
	.item{
		height: 24px;
		line-height: 35px;
		overflow: hidden;
		word-break: break-all;
		text-overflow: ellipsis;
		display: inline-block;
	}
	.itemaddress{
		width: 400px;
	}
	.itemlinkman{
		width: 120px;
	}
</style>




