<template>
  <div class="">
    <template v-if='isType == 1'>
      <Head2 :isCur='1' isNavShow = true isSeachShow = true isHotLine = 'true'></Head2>
      <panel-title :crumbs="[
        { name:'首页', 
          route:{
            path:'/classpage',
            query:{
              type:1
            }
          }, 
        },
        { name:'免费学', 
          route:{
            path:'/classpage',
            query:{
              type:1
            }
          }, 
        }
      ]" />
    </template>
    <template v-else>
      <Head2 :isCur='2' isNavShow = true isSeachShow = true isHotLine = 'true'></Head2>
      <panel-title :crumbs="[
        { name:'首页',
          route:{
            path:'/classpage',
            query:{
              type:0
            }
          }, 
        },
        { name:'课程中心', 
          route:{
            path:'/classpage',
            query:{
              type:0
            }
          }, 
        }
      ]" />
    </template>
    
    <section class="">
      <!--面包屑导航-->
      <!-- <div class="pathline container">
        <el-breadcrumb separator-class="el-icon-arrow-right" class="bread">
          <el-breadcrumb-item>
            <span @click="breadClick(0)">{{$t('Main.classpage.AllCourse')}}</span>
          </el-breadcrumb-item>
          <el-breadcrumb-item v-for="(item,index) in filterCourseType" :key="index">
              <span style="cursor:pointer" @click="breadClick(item.category_id)">{{item.categoryname}}</span>
          </el-breadcrumb-item>
        </el-breadcrumb>
      </div> -->

      <!--列表页头部信息-->
      <div class="classList container">
        <div class="typeList">
          <div class="type left">分类<span></span></div>
          <!--一级-->
          <ul class="moretype" >
            <li
              @click="selectCateId(1,data.id,index);"
              v-for="(data,index) in SlideListt" :key="index"
              :class="{'firsttype':data.selected}"
              >
              <span>{{data.title}}</span>
            </li>
          </ul>
        </div>
        <!--二级-->
        <div class="typeList" v-if = 'SlideListt2.length >0'>
          <div class="type left">阶段<span></span> </div>
          <ul class="moretype">
            <li
              @click="selectCateId(2,data.id,index)"
              v-for="(data,index) in SlideListt2" :key="index"
              :class="{'firsttype':data.selected}"
            >
              <span>{{data.title}}</span>
            </li>
          </ul>
        </div>
        <!--三级-->
        <div class="typeList" v-if = "SlideListt3.length > 0">
          <div class="type left">年级<span></span> </div>
          <ul class="moretype">
            <li
              @click="selectCateId(3,data.id,index)"
              v-for="(data,index) in SlideListt3" :key="index"
              :class="{'firsttype':data.selected}"
            >
              <span>{{data.title}}</span>
            </li>
          </ul>
        </div>
        <!--类型-->
        <div class="typeList">
          <div class="type left">课程模式<span></span> </div>
          <ul class="moretype">
            <li
              @click="selectPattern(index)"
              v-for="(data,index) in patternList" :key="index"
              :class="{'firsttype':patternIndex == index }"
            >
              <span>{{data}}</span>
            </li>
          </ul>
        </div>
      </div>

      <div class="global container" :style="{minHeight: fullHeight-82-76-285+'px'}">
        <div class="classtitle">
          <p class='left'>课程列表</p>
          <p class='right'>共 <span>{{total}}个</span> 课程</p>
        </div>
        <!--列表页数据-->
        <courseList1 :filterCourserList='filterCourserList'></courseList1>
      </div>
      <Page :total="total" :current-page='current' :limit="limit" @pagechange="pagechange" v-if = 'total!= 0'></Page>
    </section>
    <Feet></Feet>
    <v-backTop />
  </div>
</template>
<script>
import panelTitle from "@/component/panelTitle";
import Head2 from "@/component/header2";
import Server from "@/service/request";
import Path from "@/service/Path";
import Page from "@/component/page";
import Feet from "@/component/feet";
import backTop from "@/component/backTop";
import courseList1 from "@/component/courselist1";
export default {
  data() {
    return {
      filterCourserList: [], //列表数据
      leader:['','',''],
      filterCourseType:[],//面包屑导航
      RecommendCourser: [],
      activeCateId:this.$route.query.id,  //当前课程id

      //分页
      total: 0, //总条数
      limit: 20, //每页显示条数
      current: 1, //当前页数

      //分类数据
      SlideListt: [], //分类数据
      SlideListt2:[],//二级
      SlideListt3:[],//3级

      l1Id:0,//当前选中类型id
      l2Id:0,
      fullHeight:0,

      isType:this.$route.query.type,
      patternList:['不限','录播','直播',],
      patternIndex:0,
    };
  },
  components: {
    panelTitle,
    Head2: Head2,
    Page,
    Feet,
    courseList1,
    'v-backTop':backTop,
  },
  mounted() {
    this.fullHeight = document.body.clientHeight;
  },
  created() {
    let {id}=this.$route.query;
    this.getPageList(id,1);
    this.getCateLeader(id);
  },
  watch:{
    $route(to,from) {
      let {id}=this.$route.query;
      this.isType = this.$route.query.type;
      this.getPageList(id,1);
      this.activeCateId = id;
      this.getCateLeader();
    }
  },
  methods: {
    //面包屑导航
    // breadClick(id){
    //   this.activeCateId = id,
    //   this.getPageList(id,1)
    //   this.getCateLeader();
    // },
    //点击分类 切换列表
    selectCateId(type,id,index){
      this.activeCateId = id;
      this.patternIndex = 0;
      this.current = 1;
      this.getCateLeader()
      this.getPageList(this.activeCateId,1);
    },
    // 点击课程模式
    selectPattern(i){
      this.patternIndex = i;
      this.getCateLeader()
      this.getPageList(this.activeCateId,1);
    },
    //获取头部面包屑
    getCateLeader(){
      Server.post(
        Path.getCateLeader,
        {
          category_id:this.activeCateId,
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
            this.SlideListt3 = [];
            this.SlideListt2 = [];
            this.SlideListt = [...data.catetree];

            for (let index = 0; index < this.SlideListt.length; index++) {
              const element = this.SlideListt[index];
              if(element.selected){
                this.searchData(element.id);
                if(element.children.length > 0){
                  for (var i = 0; i < element.children.length; i++) {
                    var e = element.children[i];
                    if(e.selected){
                      this.searchData(element.id,e.id);
                    }
                  }

                }
              }
            }
            this.filterCourseType = data.leader;
            let tmp = data.leader;
            tmp = tmp.map((el,i) => {
              tmp = el.categoryname;
              return tmp;
            })
            this.leader = tmp;
          }
        }
      );
    },
    //获取分类数据
    searchData(l1id=0,l2id=0){
      let d1 = 0;
      let d2 = 0;
      let { SlideListt } = this;
      for (let i = 0; i < SlideListt.length; i++) {
        const el = SlideListt[i];
        if(el.id == l1id){
          for (let index = 0; index < el.children.length; index++) {
            const ele = el.children[index];
            if(ele.id == l2id){
             this.SlideListt3 = [...ele.children];
            }
          }
          this.SlideListt2 = [...el.children];
        }
      }
    },
    //分页
    pagechange(currentPage) {
      this.getPageList(this.activeCateId,currentPage);
    },
    //课程课表 数据
    getPageList(id,currentPage) {
      Server.post(
        /*
          "is_free": "是否查询免费学。0:全部.1:免费"
          "coursetype": "可选1 录播课 2直播课
        */
        Path.getFilterCourserList,
        {
          'is_free':this.isType,
          'category_id':id,
          'limit':this.limit,
          pagenum:currentPage,
          coursetype:this.patternIndex
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
            this.filterCourserList = data.schedulist;
            this.total = data.pageinfo.total;
            this.current = Number(data.pageinfo.pagenum);
            this.limit = Number(data.pageinfo.pagesize);
          }
        }
      );
    },
  }
};
</script>
<style scoped lang="less">
@import "~@/assets/css/base";
.el-pagination.is-background .el-pager li:not(.disabled).active {
  background-color: rgba(84, 180, 116, 1);
  color: #fff;
}
.classtitle {
  margin: 40px 60px 0;
  height: 30px;
  line-height: 30px;
  border-bottom: 1px solid @color1;
  p.left{
    width: 100px;
    text-align: center;
    font-size: 16px;
    color: #fff;
    background: @color1;
  }
  p.right{
    font-size: 13px;
    color: #666;
    span{
      color: @color1;
    }
  }
}
.pathline {
  span {
    width: auto;
    height: 14px;
    font-size: 14px;
    color: #818181;
    cursor: pointer;
    &:hover{
      text-decoration: underline;
    }
  }
  .el-breadcrumb__item:last-child span{
    color: @color1;
  }
  .bread {
    margin-left: 25px;
    padding: 38px 0 42px;
  }
}

// 分类
.classList {
  background: #fff;
  padding-top: 15px;
  overflow: hidden;
  .typeList {
    line-height: 30px;
    margin-bottom: 20px;
    overflow: hidden;
    .type {
      margin-left: 86px;
      margin-right: 14px;
      border: 1px solid #ccc;
      border-radius:4px; 
      width: 78px;
      height: 30px;
      padding: 0 10px;
      color: #4D4D4D;
      text-align: justify; 
      overflow: hidden;
      &>span{
        display: inline-block; padding-left: 100%;
      }
    }
    .moretype {
      margin-left: 200px;
      li {
        float: left;
        padding: 0 20px;
        font-size: 16px;
        cursor: pointer;
        transition: all .3s;
        color: #4D4D4D;
        span {
          width: 100%;
          display: block;
        }
      }
      li.firsttype, li.firsttype:hover{
        color:#fff;
        color: @color1;
      }
      li:hover{
        color:@color1;
      }
    }
  }
}
//列表
.global {
  margin: 20px  auto; 
  background: #fff;
  overflow: hidden;
}
</style>



