<template>
  <div class="">
    <Head2 :isCur='3' isNavShow = true isSeachShow = true isHotLine = 'true'></Head2>
    <panel-title :crumbs="[
      {
        name:'首页',
        route:'',
      },{
        name:'名师堂',
        route:'',
      }
    ]" />
    <section class="container">
      <!--列表页数据-->
      <div class="content" :style="{minHeight: fullHeight-515+'px'}">
        <div  v-if='teacherList.length > 0'>
          <div class="mediaBox mediaBox2 left" v-for="item in teacherList" :key="item.schedulingid"> 
            <router-link :to="{path:'teacherDetails',query: {id:item.teacherid}}" class="media">
              <div class="mediaImg">
                <img :src="item.imageurl || defaultTeacher" alt="" @error = "_imageError"/> 
              </div>
              <div class="mediaCon">
                <p class="mediaName line1">{{item.teachername}}</p>
                <p class='profile line1'>{{item.profile}}</p>
              </div>
            </router-link>
          </div>
        </div>
        <!-- 暂无数据 -->
        <defaultShow v-else :text="$t('Main.ClassInfo.noData')"/>
      </div>

      <!--分页-->
      <Page :total="total" :current-page='current' :limit="limit" @pagechange="pagechange" v-if = 'total!= 0'></Page>
    </section>
    <Feet></Feet>
    <v-backTop />
  </div>
</template>
<script>
import panelTitle from "@/component/panelTitle";
import Head2 from "@/component/header2";
import Server from "@/service/request";
import Path from "@/service/Path";
import Page from "@/component/page";
import Feet from "@/component/feet";
import defaultShow from "@/component/defaultShow";
import defaultTeacher from "@/assets/images/defaultTeacher.png";
import backTop from "@/component/backTop";
export default {
  data() {
    return {
      teacherList: [], //列表数据
      fullHeight:0,
      defaultTeacher,
      //分页
      total: 0, //总条数
      limit: 20, //每页显示条数
      current: 1, //当前页数
    };
  },
  components: {
    panelTitle,
    Head2,
    Page,
    Feet,
    defaultShow,
    'v-backTop':backTop,
  },
  created() {
    this.getAllTeacherList(this.current);
    this.fullHeight = document.body.clientHeight;
  },
  methods: {
    //分页
    pagechange(currentPage) {
      this.getAllTeacherList(currentPage);
    },
    getAllTeacherList(currentPage) {
      Server.post(
        Path.getAllTeacherList, {
          'pagenum':currentPage,
        },
        res => {
          let { code, data } = res;
          if(code === 0) {
            this.teacherList = data.data;
            this.total = data.pageinfo.total;
            this.current = Number(data.pageinfo.pagenum);
          }
        }
      );
    },
  }
};
</script>
<style scoped lang="less">
@import "~@/assets/css/base";
@import "~@/assets/css/list";
.classBox li.item:hover{
  cursor: inherit;
}
.classtitle {
  width: 100%;
  height: 66px;
  line-height: 66px;
  overflow: hidden;
  padding-left: 30px;
  font-size: 14px;
  color: @color2;
  span{
    color: @color1;
  }
}
</style>



