<template>
	<!--可用课程-->
	<div class='send-wrapper classBox container' style='margin-top:50px;'>
		<div class='tit'>
			<span v-if='packageDetail.trialtype == 2'>可用分类</span>
			<span v-else>可用课程</span>
		</div>
		<ul class="classItems">
			<template >
				<li class='item item2' v-if='packageDetail.trialtype == 1'>
					<div class='tagArr'>
						<router-link :to="{path:'classpage',query:{type:0}}">
							<el-tag>
								全部课程可使用
							</el-tag>
						</router-link>
					</div>
				</li>
				<li class='item item2' v-if='packageDetail.trialtype == 2'>
					<div v-for = '(e,i) in packageDetail.categoryarr' :key = 'i' class='tagArr'>
						<router-link :to="{path:'classpage',query:{id:e.id,type:0}}" v-if='e.child.length == 0'>
							<el-tag>
								{{e.categoryname}}
							</el-tag>
						</router-link>
						<span v-for = '(el,i) in e.child' :key = 'i' v-else>
							<router-link :to="{path:'classpage',query:{id:el.id,type:0}}" v-if='el.child.length == 0' >
								<el-tag>
									{{e.categoryname}}-{{el.categoryname}}
								</el-tag>
							</router-link>
							<span v-for = '(eli,i) in el.child' :key = 'i' v-else>
								<router-link :to="{path:'classpage',query:{id:eli.id,type:0}}" >
									<el-tag>
										{{e.categoryname}}-{{el.categoryname}}-{{eli.categoryname}}
									</el-tag>
								</router-link>
							</span>
						</span>
					</div>
				</li>
				<li class='' v-if='packageDetail.trialtype == 3'>
					<div class="classBox classBox2  bg-white mb-20">
						<courseList2 :Courser='packageDetail.courselist' v-if='packageDetail.courselist.length>0 && packageDetail.courselist.length<5' :type='3'></courseList2>
						<courseList2 :Courser='packageDetail.courselist' v-if='packageDetail.courselist.length>4' :type='2'></courseList2>
					</div>
				</li>
			</template>
		</ul>
	</div>	
</template>
<script>
	import courseList2 from "@/component/courselist2";
	export default {
		props:['packageDetail'],
		components:{
			courseList2,
		},
	};
</script>
<style lang="less" scoped>
	@import "../../courseDetails/assets/css/courseDetails";
	@import "~@/assets/css/list2";
	//套餐详情页 课程列表
	.classBox2{
		margin: 0 60px;
		.mediaBox{
			padding: 26px 0;
			&:hover{
				border-color: transparent;
				img{
					transform: scale(1.2);
				}
			}
		}
	}
	.classBox  .classItems{
		.item2 p a{
			display: inline-block;
		}
		li.item2{
			border:none !important;
			padding: 14px 0;
			margin: 20px 120px;
		}
		
	}
	.tagArr{
		display: inline-block;
		span{
			display: inline-block;
		}
	}
	.el-tag{
		padding:0 10px;
		margin: 0 10px 10px 0;
	}
</style>



