<template>
  <div>
	  <Head2 isTitleShow = 'true' title ='填写并确认订单' isStepShow = 'true' isStepNum = '1'></Head2>
		<div class="placeOrder container">
			<h1 class="orderTitle"> 订单详情</h1>
			<div class="topBox">
				<div class="logo">
					<img :src="packageDetail.setimgpath" @error = "_imageError"/>
				</div>
				<div class="OrderInfo">
					<table  border="0" cellpadding="0" cellspacing="0">
						<thead>
							<th width='20%'>套餐名称</th>
							<th width='15%'>课时数量</th>
							<th >使用条件</th>
							<th width='15%'>有效期至</th>
							<th width='15%'>价格</th>
						</thead>
						<tbody>
							<td>{{packageDetail.setmeal}}</td>
							<td>{{packageDetail.bughour}}</td>
							<td>
								<template v-if='packageDetail.threshold != 0'>
									购买课程满{{packageDetail.threshold}}元可以使用，
								</template>
								<template v-if='packageDetail.limitbuy != 0'>
									每人限购{{packageDetail.limitbuy}}次,
								</template>
								{{packageDetail.trialtype | gettrialtype}}可使用。
							</td>
							<td>
								<!-- efftype: 有效期状态（1：时间范围 2：固定天数） -->
								<template v-if='packageDetail.efftype == 1'>
									{{packageDetail.effendtime | getTime}}
								</template>
								<template v-if='packageDetail.efftype == 2'>
									购买后{{packageDetail.efftime}}天
								</template>
							</td>
							<td>{{$t('Main.Common.symbol')}}{{packageDetail.setprice}}</td>
						</tbody>
					</table>
				</div>
			</div>
			<template v-if='packageDetail.givestatus != 0'>
				<h1 class="orderTitle" style='margin-top:30px'> 赠送</h1>
				<div class="topBox">
					<div class="logo">
						<img src="./assets/images/icon.png" @error = "_imageError"/>
					</div>
					<div class="OrderInfo">
						<table  border="0" cellpadding="0" cellspacing="0">
							<thead>
								<th width='20%'>赠送内容</th>
								<th >使用条件</th>
								<th width='15%'>有效期至</th>
							</thead>
							<tbody>
								<td >
									<template v-if='packageDetail.sendvideo != 0'>
										{{packageDetail.sendvideo}}课时录播课
									</template>
									<template v-if='packageDetail.sendlive != 0'>
										{{packageDetail.sendlive}}课时直播课
									</template>
								</td>
								<td>
									<template v-if='packageDetail.giftthreshold != 0'>
										购买课程满{{packageDetail.giftthreshold}}元可以使用，
									</template>
									{{packageDetail.gifttrialtype | gettrialtype}}可使用。
								</td>
								<td>
									<!-- giftefftype: 有效期状态（1：时间范围 2：固定天数） -->
									<template v-if='packageDetail.giftefftype == 1'>
										{{packageDetail.gifteffendtime | getTime}}
									</template>
									<template v-if='packageDetail.giftefftype == 2'>
										购买后{{packageDetail.giftefftime}}天
									</template>
								</td>
							</tbody>
						</table>
					</div>
				</div>
			</template>

			<div class="bottomBox">
				<div class="contentBox">
					<!-- 请在30分钟内支付，逾期不支付，订单自动取消。 -->
					<p class="tip">
						<icon name="warnImg"  :width="20" :height="20" class="warnImg"></icon>
						{{$t('Main.placeOrder.tip')}}</p>
					<div class="classPrice">
						<p>套餐总价：{{packageDetail.setprice}}</p>
						<p>实付价格：<span class="price">{{$t('Main.Common.symbol')}}{{packageDetail.setprice}}</span></p>
						<!-- <p>
							<input type="checkbox" name="" id="" value=""  v-model="checked" />
							<router-link target="_blank" :to="{path:'/agreement'}">《用户购买协议》</router-link>
						</p> -->
					</div>
				</div>
				<div class="placeBox">
					<!-- 提交订单 -->
					<p class="orderBtn" @click="PackagesgotoOrder()">{{$t('Main.placeOrder.orderBtn')}}</p>
				</div>
			</div>
		</div>
		<Feet></Feet>
  </div>
</template>
<script>
import Server from "@/service/request";
import Path from "@/service/Path";
import Head2 from '@/component/header2'
import Feet from '@/component/feet'
import moment from "moment";
export default { 
	data(){
		return{
			// checked:false,
			ordernum:'',//订单号
			packageDetail:[],
		}
	},
	components: {
		Head2,
		Feet
	},
	created(){
		this.getPackageDetail();
	},
	methods:{
		//获取详情页数据
		getPackageDetail() {
			Server.post(
				Path.getPackageDetail, {
					packageid: this.$route.query.id,
				},
				res => {
					let { code,data } = res;
					if(code === 0) {
						this.packageDetail = data;//所有的数据
					}
				}
			);
		},

		//提交订单
	  PackagesgotoOrder(){
		  // if(this.checked==false){
			// 		this.$message({
			// 			message:'请签定用户购买协议',
			// 			type:'warning'
			// 		})
			// return false ;
			Server.post(Path.PackagesgotoOrder,{
				'packageid':this.$route.query.id,
				'ordersource':'1',//"下单渠道 1web 2app"
			},res=>{
				let {code,data}=res;
				if(code===0){
					this.$router.push({
						path:'/comboPaymentCenter',
						query:{
							ordernum:data.ordernum
						}
					})	
				}else{
					this.$message.error(res.info);
				}
			})	
		},
	},  
}
</script>
<style lang="less" scoped>
  @import '../placeOrder/assets/css/placeOrder';
</style>




