<template>
	<div class="">
		<Head2 ref="child" :isCur='2' isNavShow = true isSeachShow = true isHotLine = 'true'></Head2>
		<panel-title :crumbs="[
      {
        name:'首页',
         route:{
            path:'/classpage',
            query:{
              type:0
            }
          }, 
      },
			{ name:'课程中心', 
				route:{
					path:'/classpage',
					query:{
						type:0
					}
				}, 
			},
			{
        name:'课程详情',
        route:'',
      }
    ]" />

		<!--面包屑导航-->
		<!-- <div class="container">
			<ul class="tag-nav">
				<li  v-for="(item,index) in category" :key = 'index'>
					<router-link :to="{path:'Classpage',query: {id:item.category_id}}">{{item.categoryname}}</router-link>
					<span><i class="el-breadcrumb__separator el-icon-arrow-right"></i></span>
				</li> 
			</ul>
		</div> -->
		<!--头部机构详情-->
		<v-lubo  @goLogin='goLogin' @getInfo="getInfo"></v-lubo>
		<!--课程详情-->
		<v-details :classinfo='classinfo' :teacherInfo='teacherInfo'></v-details>
		<Feet></Feet>
	</div>
</template>
<script>
	import panelTitle from "@/component/panelTitle";
	import Head2 from "@/component/header2";
	import Feet from '@/component/feet'
	import Server from "@/service/request";
	import Path from "@/service/Path";
	import lubo from './lubo';
	import details from './details';
	export default {
		components: {
			panelTitle,
			Head2,
			Feet,
			'v-details':details,
			'v-lubo':lubo,
		},
		data() {
			return {
				classinfo:{},//班级信息
				teacherInfo:{},//老师信息
			};
		},
		methods: {
			//登录
			goLogin(){
				this.$refs.child.goLogin(0);
			},
			//筛选获取数据
			getInfo(val,i){
				this.teacherInfo = val;
				this.classinfo = i;
			},
		},
	};
</script>
<style lang="less" scoped>
</style>



