<!--进度条 123步-->
<template>
  <div class="steps" id="step">
    <div :class="['step-item','active']">
      1
      <div class="desc">
        {{desc[0]}}
      </div>
    </div>
    <div :class="['line',{'line-active':step == 2 || step == 3}]"></div>
    <div :class="['step-item',{'active':step == 2 || step == 3}]">
      2
      <div class="desc">
        {{desc[1]}}
      </div>
    </div>
    <div :class="['line',{'line-active':step == 3}]"></div>
    <div :class="['step-item',{'active':step == 3}]">
      3
      <div class="desc">
        {{desc[2]}}
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "step",
  data: () => ({

  }),
  props: {
    step: {  // 必须提供字段
      type: String,
      default: 3,
      required: true
    },
    desc: {
      type:Array,
      default: [
        '填写课程信息', //..
        '填写课时信息',//..
        '填写课时信息'//
      ]
    }
  }
}
</script>
<style scoped lang='less'>
@import "~@/assets/css/base";
  .steps{
    position: relative;
    height: 48px;
  }
  .steps *{
    display: inline-block;
    vertical-align: middle;
  }
  .step-item{
    width: 43px;
    height: 43px;
    border-radius: 50%;
    text-align: center;
    font-size: 21px;
    line-height: 43px;
    color: @color1;
    position: absolute;
    z-index: 2;
    background-color: #F4F4F4;
  }
  .steps .step-item:nth-of-type(1){
    left: 0;
  }
  .steps .line:nth-of-type(2){
    top:50%;
    margin-top:-1px;
    left: 0;
  }
  .steps .step-item:nth-of-type(3){
    left: 50%;
    margin-left: -23px;
  }
  .steps .line:nth-of-type(4){
    top:50%;
    margin-top:-1px;
    right: 0;
  }
  .steps .step-item:nth-of-type(5){
    right: 0;
  }
  .desc{
    position: absolute;
    top: 55px;
    left: -40px;
    width:120px;
    display: block!important;
    font-size: 12px;
    // color: #5e6266!important;
    line-height: 18px!important;
    color:@color1;
  }
  .line{
    position: absolute;
    z-index: 1;
    width: 300px;
    height: 2px;
    background:#62BAFE;
  }
  .line-active{
    background: #62BAFE;
  }
  .active{
    background: @color1;
    color:#fff;
  }


</style>



