<template>
  <div class="classification" v-on:mouseleave="clearimg()">
    <!--分类 v-on:mouseleave="clearimg()"-->
    <div class=''>
      <div class="classify flex-box" >
        <ul class="classList">
          <li 
            class="flex-box typeMore" 
            v-for="(el, i) in SlideListt" 
            :key="el.category_id"
            @mouseover="changeimg(i);el.active = true"
            @mouseout="clearimg(i);el.active = false"
            @click.prevent.stop="toClassifyList(el.category_id)"
            :class="{selected:i === curIndex}"
          >
            <div class="span">
              <img :src='el.icostwo' v-if='el.active != true'/>
              <img :src='el.icos' v-else/>
              <span class='line1'>{{el.categoryname}}</span>
            </div>
            <div class="classContent" v-show="el.active" >
              <ul class="level2 ">
                <li v-for="(e, i) in el.child " :key="i" @click.prevent.stop="toClassifyList(e.category_id)">
                  <div class="classTitle span">{{e.categoryname}}</div>
                  <ul class="level3">
                    <li v-for="(item, sub) in e.child" :key="sub" @click.prevent.stop="toClassifyList(item.category_id)">
                      {{item.categoryname}}
                    </li>
                  </ul>
                </li>
              </ul>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
import Server from "@/service/request";
import Path from "@/service/Path";
export default {
  data(){
    return{
      SlideListt: [],//一级课程分类
      curIndex:-1,
    }
  },
  created(){
    this.getClassifyList();
  },
  methods:{
    //  跳转到分类列表
    toClassifyList(id) {
      this.$router.push({
        path: "/classpage",
        query: {
          id: id,
          type:0,
        }
      });
    },
    changeimg(index) {
      this.curIndex = index;
    },
    clearimg() {
      this.curIndex = -1;
    },
    //   查询一级分类
    getClassifyList() {
      Server.post(
        Path.getCategoryList,
        {},
        res => {
          if (res.code === 0) {
            this.SlideListt = res.data.map((el,index)=>{
              if(index == 0){
                el.active = false;
              }else{
                el.active = false;
              }
              return el;
            });
          }
        }
      );
    },
  },
}
</script>
<style scoped lang="less">
@import "~@/assets/css/base";
.classification{
  position: absolute;
  top: 39px;
  left: 0;
  z-index: 999;
}
.classify {
  .classList {
    width: 200px;
    background:rgba(65,177,230,.85);
    li {
      padding: 0 24px 0 38px;
      line-height: 70px;
      font-size: 16px;
      color: #fff;
      cursor: pointer;
      .span {
        display: inline-block;
        span{
          height: 70px;
          overflow: hidden;
          display: inline-block;
          vertical-align: top;
          width: 100px;
          word-break: keep-all;
        }
        img {
          display: inline-block;
          width: 16px;
          height: 16px;
          vertical-align: top;
          margin: 26px 13px 0 0;
          border:0;
        }
        img[src=""],img:not([src]){
          opacity:0;
          border:0;
        }
      }
    }
    li+li{
      border-top: 1px solid #fff;
    }
  }
  .classContent {
    width: 730px;
    height: 100%;
    overflow-y: auto;;
    position: absolute;
    left: 200px;
    top:0;
    background: rgba(255,255,255,.95);
    .level2 {
      padding: 43px 40px 0 50px;
      overflow: hidden;
      li {
        overflow: hidden;
        margin: 0 0 16px 0;
        padding: 0;
        font-size: 13px;
        line-height: 13px;
        cursor: pointer;
        border-top: none;
        .classTitle {
          display: block;
          width: 38px;
          color: #1A1A1A;
          font-weight: bold;
          float: left;
          margin-bottom: 20px;
          cursor: pointer;
          font-size: 12px;
          border-right: 1px solid #A6A6A6;
          margin-right: 20px;
          height: 12px;
          padding-right: 20px;
          &:hover{
            color:@color1;
          }
        }
      }
      .level3 {
        display: block;
        margin-left: 80px;
        word-spacing:0;
        li {
          margin: 0 30px 16px 0;
          cursor: pointer;
          color: #1A1A1A;
          width: auto;
          float: left;
          line-height: 14px;
          font-size: 12px;
            &:hover{
              color:@color1;
            }
        }
      }
    }
    .typeClass {
      margin-top: 44px;
      height: 70px;
      width: 100%;
      .classTitle {
        width: 50px;
        height: 13px;
        font-size: 13px;
        color: rgba(51, 51, 51, 1);
        margin-left: 52px;
        margin-right: 70px;
        font-weight: bold;
      }
      ul {
        li {
          margin-right: 56px;
        }
      }
    }
  }
}
.selected {
  background: #fcfcfc;
  .span {
    color: @color1;
  }
}
</style>



