<template>
	<div class="">
		<!--头部机构详情-->
		<div class="container">
			<div class="topBoxContent">
				<div class="contentLeft">
					<div class='imgBox' v-if="picShow">
						<img :src="classinfo.imageurl" :alt='classinfo.coursename' class="titleImg" @error = "_imageError">
						</div>
					<!-- <div class="share">
						<span>分享到</span>
						<img src="./assets/images/wx.png" />
						<img src="./assets/images/pyq.png">
						<img src="./assets/images/kj.png">
						<img src="./assets/images/qq.png">
						<img src="./assets/images/wb.png">
					</div> -->
				</div>
				<div class="contentRight">
					<div class="title">
						{{classinfo.coursename}}
						<span class='type'>{{classinfo.classtypes | changeType}}</span>
					</div>
					<p class="desc">{{classinfo.subhead}}</p>
					<p class="sendGift">
						<template v-if='this.gift.giftstatus == 0 && this.classType == 2'>
							<span >赠送教材</span>
							<label v-for ='(el,i) in gift.giftdescribe' :key='i'>
								{{el.name}} x {{el.num}}
							</label>
						</template>
					</p>
					<p class="priceP">
						课程价格
						<span class="price">
							<label style='font-size:16px'>{{$t('Main.Common.symbol')}}</label>{{classinfo.totalprice}}<label style='font-size:16px'>/课时</label>
						</span>
					</p>
					<!--直播课选择规格-->
					<div class='' v-if='this.classType == 2' style='overflow: hidden'>
						<dl class='rules rules1'>
							<dt>日期</dt>
							<dd>
								<el-select v-model="date" placeholder="请选择">
									<el-option
										v-for="(item,i) in teacherArr"
										:key="i"
										:label="item.date"
										:value="item.date"
									>
										<span class='selectTime' 	@click='selectTime(item.date,i)'>{{item.date}}</span>
									</el-option>
								</el-select>
							</dd>
						</dl>
						<dl class='rules rules2'>
							<dt>类型</dt>
							<dd>
								<span
									v-for = "(el,i) in fullpeopleArr"
									:key = 'i'
									:class='{cur:typeArrIndex == i}'
									@click="toggleType(i,el)">
									1v{{el}}
								</span>
							</dd>
						</dl>
						<dl class='rules rules3'>
							<dt>班级</dt>
							<dd>
								<span 
									v-for = "(el,i) in classArr"
									:key = 'i'
									:class='{cur:classArrIndex == i,disable:!el.isClick}'
									@click="toggleClass(i,el.isClick)" @mouseover="el.isFlag=true" @mouseout="el.isFlag=false">
									{{el.name}}
									<!-- classstatus：3 满员 -->
								  <em v-if='el.status == true'>满</em>
									<label class='tooltip' v-if='el.isFlag && !el.isClick'>
										本班级暂未开放报名
										<div x-arrow="" class="popper__arrow" style="left: 113.5px;"></div>
									</label>
								</span>
							</dd>
						</dl>
					</div>
					<button 
						class="btn-save"
						@click="signUp()" 
						v-if = 'classinfo.applystatus == 1'>
						<!-- 立即报名 -->
						{{$t('Main.courseDetails.apply')}}
					</button>
					<a 
						class="btn-save"
						v-if = 'classinfo.applystatus == 2'
						:href='url2'
						target="_blank">
						开始学习
					</a>
					<button 
						class="btn-save btn-save2 "
						v-if = 'classinfo.applystatus == 0'>
						<!-- 暂停报名 -->
						{{$t('Main.courseDetails.Suspend')}}
					</button>
					<div class='focus-btn' v-if = 'isCollect == 0' @click = 'classCollect'>
						<i></i>
						<!-- 收藏 -->
						{{$t('Main.ClassInfo.Collect')}}
						</div>
					<div class='focus-btn focus-btn2' v-if = 'isCollect == 1' @click = 'cancelCollect'>
						<i></i>
						<!-- 取消收藏 -->
						{{$t('Main.ClassInfo.CancelCollect')}}
					</div>

				</div>
			</div>
		</div>
	</div>
</template>
<script>
	import Server from "@/service/request";
	import Path from "@/service/Path";
	import moment from "moment";
	export default {
		props:[],
		data() {
			return {
				picShow:false,
				classArr:[],//所有班级名称集合
				classArrIndex:0, //班级名称索引
				fullpeopleArr:[],//当前班级人数集合
				fullpeopleArrAll:[],//所有课程人数集合
				typeArrIndex:0, //当前班级人数索引
				teacherArr:[], //所有可选日期
				date:'',//所选日期
				fullpeople:'',//班级人数
				
				classinfo:{},//单个班级信息
				allClass:[],//所有班级信息
				teacherInfo:{},//老师信息
				isCollect:0,//是否收藏
				classType:'', //课程类型
				url2:'',//开始学习
				gift:{ //是否赠送教材
					giftdescribe:[],
					giftstatus:1,//0赠送1不送
				},
			};
		},
		mounted() {
			this.getCurriculumDateList();
		},
		methods: {
			changeimg(index) {
				this.curIndex = index;
			},
			// 选择日期
			selectTime(val,i){
				this.typeArrIndex = 0;
				this.classArrIndex = 0;
				this.date = val;
				this.fullpeopleArr = this.fullpeopleArrAll[i]
				this.fullpeople = this.fullpeopleArr[0]
				this.getCurriculumInfo(this.date,this.fullpeople,0)
			},
			// 切换班级类型
			toggleType(i,el){
				this.typeArrIndex = i;
				this.classArrIndex = 0;
				this.fullpeople = el;
				this.getCurriculumInfo(this.date,this.fullpeople,0)
			},
			// 切换班级名称
			toggleClass(i,states){
				if(states==true){
					this.classArrIndex = i;
					this.getCurriculumInfo(this.date,this.fullpeople,this.classArrIndex)
				}
			},
			// 课程详情返回可选日期
			getCurriculumDateList(){
				Server.post(Path.getCurriculumDateList,{
					'courseid':this.$route.query.id,
				},res=>{
					let {code,data}=res;
					if(code===0){
						if(data.length>0){ //直播课
							this.teacherArr = data;
							this.date = this.teacherArr[0].date;
							// this.date = this.teacherArr[0].date;
							let arr = [];
							this.teacherArr.map((el,i) => {
								arr.push(data[i].class)
							})
							this.fullpeopleArrAll = arr ;
							this.fullpeopleArr = this.fullpeopleArrAll[0]
							this.fullpeople = this.fullpeopleArr[0]
							this.getNewDate();
						}else if (data.length == 0){
							this.getCurriculumInfo(this.date,this.fullpeople,this.classArrIndex) //录播课
						}
					}else{
						this.$message.error(res.info)
					}
				})	
			},

			// 获取最新日期
			getNewDate(){
				let {teacherArr} = this;
				let date1 = moment().format('X')
				for(var key in teacherArr){
					let tmp = moment(teacherArr[key].date).format('X');
					if(tmp - date1  >= 0){
						this.date = teacherArr[key].date;
						this.fullpeopleArr = this.fullpeopleArrAll[key]
						this.fullpeople = this.fullpeopleArr[key]
						this.getCurriculumInfo(this.date,this.fullpeople,this.classArrIndex)
						return;
					}
				}
				this.getCurriculumInfo(this.date,this.fullpeople,this.classArrIndex)
			},

			//获取详情页数据
			getCurriculumInfo(date,fullpeople,classArrIndex) {
				Server.post(
					Path.getCurriculumInfo, {
						courseid: this.$route.query.id,
						teacherid:this.$route.query.teacher || '',
						date:date,
						fullpeople:fullpeople,
					},
					res => {
						let {code,data} = res;
						if(code === 0) {
							// 录播课
							if(data.classinfo==undefined){
								this.classinfo = data;
								this.teacherInfo = data.teacher;
								this.classType = 1;
							}else{
								this.classType = 2;
							
								this.classArr=[]
								this.gift.giftstatus = data.giftstatus;
								if(this.gift.giftstatus == 0){//赠送教材
									this.gift.giftdescribe = data.giftdescribe;
								} 
								this.allClass = data.classinfo;
								this.classinfo = data.classinfo[classArrIndex];
								this.teacherInfo = data.classinfo[classArrIndex].teacher;

								// 获取类型和班级
								let arrClick = [true];
								data.classinfo.map((el,i) => {
									let status = false,isClick;
									// if(this.$loginInfo == null || this.$loginInfo ==''){
									// 	if(el.applystatus == 0){//暂停报名
									// 		arrClick.push(true)
									// 	}else{
									// 		arrClick.push(false)
									// 	}
									// }else{
									// 	if(el.classstatus == 3 || el.applystatus != 1){//满员,过期，已报名，
									// 		arrClick.push(true)
									// 	}else{
									// 		arrClick.push(false)
									// 	}
									// }

									//  'classstatus 班级状态 0 未招生 1已招生 2已成班 3已满员 4授课中 5已结束(已取消)  6 已超时'
									if(el.classstatus != 0 || el.applystatus != 1){//暂停报名
										arrClick.push(true)
										arrClick[i] = true;
									}else{
										arrClick.push(false)
									}

									if(el.classstatus == 3){ //满员
										status = true;
										// arrClick[i] = true;
									}
									let tmpArr = {'name':el.gradename,isClick:false,status:status,isFlag:false}
									this.classArr.push(tmpArr)
								})
								this.classArr.map((e,i) => {
									e.isClick = arrClick[i]
								})
							}
							this.isCollect = data.is_collect;
							this.$emit('getInfo',this.teacherInfo,this.classinfo)

							// 已报名 去学习
							if(this.classinfo.applystatus ==2){
								
								if(this.classinfo.classtypes ==1){//录播课
									this.url2 = this.$webIp+'/student/middleWare/index.html?type=5&studentinfo='+JSON.stringify(this.$loginInfo)+'&key='+window.localStorage.getItem('studentkey')+'&schedulingid=""'+'&curtype='+this.classinfo.classtypes+'&id='+this.classinfo.courseid;
								}else if(this.classinfo.classtypes ==2){ //直播课
									this.url2 = this.$webIp+'/student/middleWare/index.html?type=6&studentinfo='+JSON.stringify(this.$loginInfo)+'&key='+window.localStorage.getItem('studentkey')+'&schedulingid='+this.classinfo.scheduid+'&curtype='+this.classinfo.classtypes+'&id='+this.classinfo.curriculumid;
								}
							}
						}
					}
				);
			},
			//跳转到提交订单页面
			signUp() {
				if(this.$loginInfo == null || this.$loginInfo ==''){
					this.$emit('goLogin')
					return false;
				}
				let totalprice = this.classinfo.totalprice;
				this.classinfo.gift = this.gift
				//设置课程信息，订单页面获取
				localStorage.setItem('classinfo', JSON.stringify(this.classinfo));
				this.$router.push({
					path: "/placeOrder",
					query: {
						id:this.$route.query.id ,//班级排课id
					}
				});
			},
			//收藏课程
			classCollect() {
				if(this.$loginInfo == null || this.$loginInfo ==''){
					this.$emit('goLogin')
					return false;
				}
				Server.post(
					Path.classCollect, {
						courseid: this.$route.query.id,
						studentid:this.$loginInfo.uid,
					},
					res => {
						let { code,		data } = res;
						if(code === 0) {
							this.$message.success(res.info);
							this.isCollect = 1;
						}else{
							this.$message.error(res.info);
						}
					}
				);
			},
			//取消收藏课程
			cancelCollect() {
				Server.post(
					Path.cancelCollect, {
						courseid: this.$route.query.id,
						studentid:this.$loginInfo.uid,
					},
					res => {
						let { code, data } = res;
						if(code === 0) {
							this.$message.success(res.info);
							this.isCollect = 0;
						}else{
							this.$message.error(res.info);
						}
					}
				);
			},
		},
		watch:{
			classinfo(val, oldVal){
				if(val.length!=0){
					this.picShow=true;
				}
			}
		},
	};
</script>
<style lang="less" scoped>
@import "../assets/css/courseDetails.less";
</style>



