<template>
  <div v-if ='data.length > 0' class='wrapper bg-white'>
    <div class="container">
      <div class='tit'>
        <h1>
          <img class="beautiful" :src="left" alt="" />
          <span>柠檬课堂</span>
        </h1>
        <ul>
          <li 
            v-for="(item,index) in recommendArr" 
            :key = "index" 
            :class='{cur:recommendArrIndex == index}'
            @click="selectType(index)"
            >
            {{item}}
          </li>
        </ul>
      </div>
      <courseList2 :Courser='recommendCourser'></courseList2>
    </div>
  </div>
</template>
<script>
import Server from "@/service/request";
import Path from "@/service/Path";
import left from './assets/images/icon.png'
import courseList2 from "@/component/courselist2";
export default {
  data(){
    return {
      data:[],
      recommendCourser:[],
      recommendArr:[],
      recommendArrIndex:0,
      left,
    }
  },
  components: {
    courseList2,
  },
  created() {
    this.getRecommendCourser();
  },
  methods: {
    //切换分类
    selectType(index) {
      this.recommendArrIndex = index;
      this.recommendCourser = this.data[index].coursearr;
		},
    // 热门
    getRecommendCourser() {
      Server.post(
        Path.getRecommendCourser,
        {},
        res => {
          let { code, data } = res;
          if (code === 0) {
            this.data = data;
            this.recommendCourser = data[0].coursearr;
            data.map((e,i) => {
              this.recommendArr.push(e.categoryname)
            })
          }
        }
      );
    },
  }
}
</script>
<style lang="less" scoped>
@import "~@/assets/css/base";
.beautiful{
  width: 27px;
  height: 27px;
  vertical-align: middle;
}
.wrapper{
  width: 100%;
  height: auto;
  .tit{
    overflow: hidden;
  }
  h1 {
    font-size: 27px;
    line-height: 27px;
    color: @color1;
    text-align: center;
    padding: 37px 0 5px 0;
    display: inline-block;
    span{
      margin: 0 5px;
    }
  }
  ul{
    display: inline-block;
    line-height: 27px;
    vertical-align: middle;
    margin: -10px 0 0 150px;
    li{
      float: left;
      display: block;
      font-size:14px;
      color: #808080;
      margin: 0 18px;
      cursor: pointer;
      &.cur,&:hover{
        color: @color1;
      }
    }
  }
}
</style>


