<template>
	<div class="">
		<Head2 ref="child" :isCur='4' isNavShow = true isSeachShow = true isHotLine = 'true'></Head2>
		<panel-title :crumbs="[
      {
        name:'首页',
        route:'',
      },{
        name:'课时套餐',
        route:'combo',
      },{
        name:'套餐详情',
        route:'',
      }
    ]" />
		<!--头部机构详情-->
		<div class="container" style='margin-bottom:50px'>
			<div class="topBoxContent">
				<div class="contentLeft">
					<div class='imgBox' v-if="picShow"><img :src="packageDetail.setimgpath" :alt='packageDetail.setmeal' class="titleImg" @error = "_imageError"></div>
				</div>
				<div class="contentRight">
					<div class="title">
						{{packageDetail.setmeal}}
					</div>
					<p class="desc desc2">
						套餐内包含{{packageDetail.bughour}}课时套餐
						<!--  
							givestatus: 是否赠送课时（0：不赠送 1：赠送) 
							sendvideo: "赠送录播课（0：未赠送 其他：赠送录播s课时间",
							sendlive: "赠送直播课（0：未赠送 其他：赠送直播s课时间）",
						-->
						<template v-if='packageDetail.givestatus == 1'>
							<template v-if='packageDetail.sendvideo != 0'>
								(赠送{{packageDetail.sendvideo}}课时录播课套餐)
							</template>
							<template v-if='packageDetail.sendlive != 0'>
								(赠送{{packageDetail.sendlive}}课时直播课套餐)
							</template>
						</template>
						<br>
						有效期为
						<!-- efftype: 有效期状态（1：时间范围 2：固定天数） -->
						<template v-if='packageDetail.efftype == 1'>
							{{packageDetail.effendtime | getTime}}
						</template>
						<template v-if='packageDetail.efftype == 2'>
							购买后{{packageDetail.efftime}}天
						</template>
					</p>
					<!-- "limitbuy": "限购次数（0：无限购，其他限购次数）",
        			"threshold": "使用门槛 0 无限制 其他代表满够金额",
					-->
					<p class="sendGift">
						购买须知：<br/>
						本套餐内容
						<template v-if='packageDetail.threshold != 0'>
							购买课程满{{packageDetail.threshold}}元可以使用，
						</template>
						<template v-if='packageDetail.limitbuy != 0'>
							每人限购{{packageDetail.limitbuy}}次,
						</template>
						{{packageDetail.trialtype | gettrialtype}}可使用，
						需在有效期截止前使用，逾期将无法使用。
					</p>
					<p class="priceP">
						套餐总价
						<span class="price">{{$t('Main.Common.symbol')}}{{packageDetail.setprice}}</span>
					</p>
					<button 
						class="btn-save"
						@click="signUp()" 
					>
						立即购买
					</button>
				</div>
			</div>
			<!--可用课程-->
			<courselist :packageDetail = 'packageDetail'></courselist>
			<!--赠送-->
			<gives v-if='packageDetail.givestatus != 0' :packageDetail = 'packageDetail'></gives>
		</div>
		<Feet></Feet>
	</div>
</template>
<script>
	import panelTitle from "@/component/panelTitle";
	import Head2 from "@/component/header2";
	import Feet from '@/component/feet'
	import Server from "@/service/request";
	import Path from "@/service/Path";
	import moment from "moment";
	import Swiper from 'swiper';
	import 'swiper/dist/css/swiper.min.css';
	import courselist from './courselist';
	import gives from './gives';
	export default {
		components: {
			panelTitle,
			Head2,
			Feet,
			courselist,
			gives,
		},
		data() {
			return {
				picShow:false,
				packageDetail:[],
			};
		},
		created() {
			this.getPackageDetail(); //获详情
		},
		methods: {
			//跳转到提交订单页面
			signUp() {
				if(this.$loginInfo == null || this.$loginInfo ==''){
					this.$refs.child.goLogin(0);
					return false;
				}
				this.$router.push({
					path: "/comboPlaceOrder",
					query: {
						id:this.$route.query.id ,//班级排课id
					}
				});
			},
			//获取详情页数据
			getPackageDetail() {
				Server.post(
					Path.getPackageDetail, {
						packageid: this.$route.query.id,
					},
					res => {
						let { code,data } = res;
						if(code === 0) {
							this.packageDetail = data;//所有的数据
							this.$nextTick(() => {
								new Swiper('.swiper-container', {
									slidesPerView: 4,
									autoplay: {
										delay: 200000,
									},
									pagination: {
										el: '.swiper-pagination',
										clickable: true,
									},
									loop: true,
									paginationClickable: true,
									observer: true 
								})
							})
						}
					}
				);
			},
		},
		watch:{
			packageDetail(val, oldVal){
				if(val.length!=0){
					this.picShow=true;
				}
			}
		},
	};
</script>
<style lang="less" scoped>
	@import "../courseDetails/assets/css/courseDetails";
</style>



