<template>
  <div class="">
		<Head2 isTitleShow = 'true' title ='课时包说明' isHotLine = 'true' ref="child"></Head2>
		<div class=" container mt-30 mb-30">
			<div class="couponBox">
				<h1 class="title">课时包说明</h1>
				<div class="items">
					<h2 class="question">课时包使用说明</h2>
					<p class="answer">
						1.购买课时包从购买之日开始计时一年（365天）有效期（即当天的24:00）<br>
						2.课时包可兑换相应年级范围内所有课程<br>
						3.课时包兑换课程不参与其他优惠活动<br>
						4.兑换课程时会按照满足当前兑换要求的课时包的购买顺序扣除（即优先扣除快到期的）<br>
						5.购买的课时包一旦开始使用即不再退还
					</p>
				</div>
				<div class="items">
					<h2 class="question">赠送课时包使用说明</h2>
					<p class="answer">
						1.不可退换<br>
						2.仅限兑换相应年级的录播课程，音频课等
					</p>
				</div>
			</div>
		</div>
		<Feet></Feet>
  </div>
</template>
<script>
import Feet from '@/component/feet'
import Head2 from '@/component/header2'
export default {
	components:{
		Head2,
		Feet
	},
}
</script>
<style lang="less" scoped>
  @import './assets/css/concessionDescription.less';
</style>




