import "babel-polyfill";
import Vue from 'vue'
import App from './App'
import router from './router';
import Vuex from 'vuex'
import ElementUI from 'element-ui'
import store from "@/store"
import 'element-ui/lib/theme-chalk/index.css'
import { JSEncrypt } from 'jsencrypt'  ; //登录加密
import defaultCoverImg from './assets/images/default.png'
import i18n from './i18n/i18n';
import Icon from 'vue-svg-icon/Icon.vue' //引用svg图片

Vue.component('icon', Icon)

Vue.use(ElementUI)
Vue.use(Vuex);

Vue.prototype.$loginInfo = JSON.parse(window.localStorage.getItem('studentinfo')) //用户信息
//ie兼容
if (window["context"] == undefined) {
  if (!window.location.origin) {
    window.location.origin = window.location.protocol + "//" + window.location.hostname + (window.location.port ? ':' + window.location.port : '');
  }
  window["context"] = location.origin + "/V6.0";
}
Vue.prototype.$webIp = window.location.origin;  //跳转ip


Vue.prototype._imageError = function (e) {
  e.target.src = defaultCoverImg;
}


if ( //退出重新登录
  localStorage.getItem('studentinfo') == null || 
  localStorage.getItem('studentinfo') == '' || 
  localStorage.getItem('studentkey') == null || 
  localStorage.getItem('studentkey') == '') 
  { 
    localStorage.removeItem('studentinfo');
    localStorage.removeItem('studentkey');
  }

Vue.config.productionTip = false

require("./assets/css/common.css")
require("./assets/css/reset.css")

// 公共过滤器
import vueFilter from './assets/js/filter'
for (let key in vueFilter) {
  Vue.filter(key, vueFilter[key])
}

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  i18n,
  store,
  components: { App },
  template: '<App/>'
})



