<template>
  <div class="">
    <Head2 isTitleShow = 'true' title ='订单完成' isStepShow = 'true' isStepNum = '3'></Head2>
    <div class="paymentCenterBox container" :style="{minHeight: fullHeight-412+'px'}">
      <!--支付成功-->
      <template v-if = '$route.query.type == 0'>
        <div class='CenterBox'>
          <div class="order-icon success"></div>
          <div class='text'>
            <h1 class="paymentCenterTitle2">
              恭喜您，支付成功！
            </h1>
            <div class='ordernum'>
              您已成功支付<span>{{$t('Main.Common.symbol')}}{{orderdata.amount}}</span>元,
              <!-- 订单编号： -->
              <label>{{$t('Main.paymentCenter.ordernum')}} {{orderdata.ordernum}}</label>
            </div>
            <a :href='url1' class="btn2" target="_blank">查看我的订单</a>
          </div>
          <div class="btn-box">
            <template v-if='buyClass == 1'>
              <router-link :to="{path:'classpage',query:{type:0}}" class="btn-back btn1">去选课</router-link>
            </template>
            <template v-if='buyClass == 2'>
              <a :href='url2' class="btn-back btn1" target="_blank">开始学习</a>
            </template>
            <router-link to="/" class="btn-back btn1">完 成</router-link>
          </div>
        </div>
        <div class="tip"> 温馨提示：您成功支付后，订单状态的更新可能存在短期的延迟，建议您十五分钟之后再查询您的支付状态 </div>
      </template>
      <!--支付失败-->
      <template v-if = '$route.query.type == 1'>
        <div class='CenterBox'>
          <div class="order-icon fail"></div>
          <div class='text'>
            <h1 class="paymentCenterTitle2">
              对不起，支付失败！
            </h1>
            <div class='ordernum'>
              <a :href='url1' class="btn-back btn2" target="_blank">查看我的订单</a>
            </div>
            <div class="btn-box">
              <!-- 重新支付 -->
              <router-link :to="{path:'paymentCenter',query: {ordernum:orderdata.ordernum}}" class="btn-back btn1">{{$t('Main.paymentSuccess.BtnPay')}}</router-link>
              <!-- 取消 -->
              <router-link to="/" class="btn-back btn2">关闭</router-link>
            </div>
          </div>
        </div>
        <div class="tip"> 温馨提示：如需咨询请拨打 023-62535196。</div>
      </template>
    </div>
		<Feet></Feet>
	</div>
</template>
<script>
import Server from "@/service/request";
import Path from "@/service/Path";
import Head2 from "@/component/header2";
import Feet from "@/component/feet";
import fail from "./assets/images/fail.png";
import success from "./assets/images/success.png";
export default {
  components: {
    Head2,
    Feet
  },
  data() {
    return {
      price:'',
      info:'',
      url1:'',
      url2:'',
      orderdata:{},
      fullHeight:0,
      buyClass:'',//购买2:课程 1:套餐
    };
  },
  created() {
    this.buyClass = this.$route.query.buyClass
    this.orderSuccess();
    // console.log(this.a)
    this.fullHeight = document.body.clientHeight;
    if(this.$loginInfo != null || this.$loginInfo != undefined){
      this.url1 = this.$webIp+'/student/middleWare/index.html?type=4&studentinfo='+JSON.stringify(this.$loginInfo)+'&key='+window.localStorage.getItem('studentkey');
    }
  },
  methods: {
    //获取订单信息
    orderSuccess() {
      if(this.buyClass == 1){ //套餐
        Server.post(Path.PackageshowOrderDetail,
          {
            ordernum:this.$route.query.order,
          },
          res => {
            let { code, data } = res;
            if (code === 0) {
              this.orderdata = data;
            }
          }
        );
      }else if(this.buyClass == 2){//课程
        Server.post(Path.showOrderDetail,
          {
            ordernum:this.$route.query.order,
          },
          res => {
            let { code, data } = res;
            if (code === 0) {
              this.orderdata = data;
              // 录播课
              if(data.classtypes ==1){
                this.url2 = this.$webIp+'/student/middleWare/index.html?type=5&studentinfo='+JSON.stringify(this.$loginInfo)+'&key='+window.localStorage.getItem('studentkey')+'&schedulingid=""'+'&curtype='+data.classtypes+'&id='+data.curriculumid;
              }else if(data.classtypes ==2){ //直播课
                this.url2 = this.$webIp+'/student/middleWare/index.html?type=6&studentinfo='+JSON.stringify(this.$loginInfo)+'&key='+window.localStorage.getItem('studentkey')+'&schedulingid='+data.schedulingid+'&curtype='+data.classtypes+'&id='+data.curriculumid;
              }
            }
          }
        );
      }
    },
  }
};
</script>
<style lang="less" scoped>
@import "./assets/css/paymentSuccess.less";
</style>



