<template>
  <div class="">
    <el-dialog 
      title="" 
      :visible.sync="dialogTableVisible" 
      class='selectLable' 
      width='440px' 
      :lock-scroll='false'
      :close-on-click-modal='false'>
      <div class='selectCate' v-if='isStep==1'>
        <div class='topTitle'>
          快速注册，领取试听课
        </div>
        <el-form class='form-select'>
          <!-- 请选择孩子的年级 -->
          <el-form-item >
            <el-select v-model="selectVal" placeholder="请选择" style='width:100%' @change = 'changeVal'>
              <el-option
              v-for="item in options"
              :key="item.categoryid"
              :label="item.name"
              :value="item.categoryid">
              </el-option>
            </el-select>
          </el-form-item>
          <el-button type="primary" @click="addUserFavorCategory()" class='btn'>下一步</el-button>
          <el-button  type="text" @click="isStep=2;getUserTag()" class='btn'>跳过</el-button>
        </el-form>
      </div>
      <div class='selectTag' v-if='isStep==2'>
        <div class='tit'>
          <h2>{{UserTag.fathername}}</h2>
          <h3>我们为您提供更精准的服务</h3>
        </div>
        <ul>
          <li 
            v-for = '(item,i) in UserTag.childarr' 
            :key = 'i' 
            :class="{'cur':item.sc}"
            @click="toggle(i)"
          >{{item.childname}}</li>
        </ul>
        <div class='tip'></div>
        <div class='btn'>
          <el-button type="primary" @click="addUserTag()" class='btn'>确定</el-button>
          <el-button  type="text" @click="dialogTableVisible=false" class='btn'>跳过</el-button>
        </div>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import Server from "@/service/request";
import Path from "@/service/Path";
export default {
  data() {
    return {
      dialogTableVisible: false,
      options: {},
      selectVal: '',
      categoryId:'',//分类id
      tagArr:[],//标签id
      UserTag:{
        childarr:[]
      },
      isStep:1,
      activeIndex:-1,
    };
  },
  mounted() {
    //获取首页课程分类
    this.getUserFavorCategory();
  },
  //计算首次注册弹出引导页
  // computed:{
  //   setFastShow(){
  //     let status = this.$store.store.state.isFastShow;
  //     if(status == true){
  //       this.dialogTableVisible = true
  //     }else{
  //       this.dialogTableVisible = false
  //     }
  //     return this.$store.store.state.isFastShow;
  //   }
  // },
  watch:{
    // setFastShow:function(val){
    //   if(val == true){
    //     this.dialogTableVisible = true
    //   }
    // },
    dialogTableVisible:function(val){
      if(val == false){
        // let { store } = this.$store;
        // store.commit('setFastShow', false);
        window.localStorage.setItem('setFastShow',false);
      }
    }
  },
  methods: {
    // 选择分类
    changeVal(val){
      this.categoryId = val;
    },
    // 选择标签
    toggle(val){
      let arr = this.UserTag.childarr;
      arr[val].sc=!arr[val].sc;
    },
    // 查询感兴趣的分类-年级
    getUserFavorCategory() {
      let tmp = window.localStorage.getItem('setFastShow');
      if(tmp == 'true'){
        this.dialogTableVisible = true;
      }else{
        return false;
      }
			Server.post(
				Path.getUserFavorCategory, {},
				res => {
					let { code, data } = res;
					if(code === 0) {
						this.options = data;
					}else{
						this.$message.error(res.info);
					}
				}
			);
    },
    // 添加学生的分类
    addUserFavorCategory() {
      if(this.categoryId == ''){
        this.$message.error('请选择学生分类');
        return;
      }
			Server.post(
				Path.addUserFavorCategory, {
          'categoryid':this.categoryId,
        },
				res => {
					let { code, data } = res;
					if(code === 0) {
            this.isStep = 2;
            this.getUserTag();
					}else{
            this.$message.error(res.info);
					}
				}
			);
    },
    //查询学生标签
		getUserTag() {
			Server.post(
				Path.getUserTag, {},
				res => {
					let { code, data } = res;
					if(code === 0) {
            let arr = data.childarr;
            arr=arr.map((e,i)=>{
              e.sc = false;
              return e;
            })
            this.UserTag = data;
					}else{
						this.$message.error(res.info);
					}
				}
			);
    },
    //添加学生标
    addUserTag() {
      let arr = this.UserTag.childarr;
      let arrId = [];
      arr = arr.map((e,i)=>{
        if(e.sc==true){
          arrId.push(e.childtagid)
        }
      })
      if(arrId.length == 0){
        this.$message.error('请选择学生标签');
        return;
      }
			Server.post(
				Path.addUserTag, {
          'tagid':this.UserTag.tagid,
          'childtags':arrId.join(','),
        },
				res => {
					let { code, data } = res;
					if(code === 0) {
						this.dialogTableVisible = false;
					}else{
						this.$message.error(res.info);
					}
				}
			);
    },
  }
};
</script>
<style scoped lang="less">
@import "~@/assets/css/base";
.selectLable .el-dialog{
  width: 440px;
  height: 552px;
  .selectCate{
    .tit{
      margin: 0 27px;
      font-size:20px;
      color: @color1;
      line-height: 86px;
      border-bottom: 1px solid #CCCCCC;
      text-align: center;
    }
    .form-select{
      margin: 0 45px;
      height: 500px;
      overflow: hidden;
      .el-form-item{
        margin: 76px 0 127px;
      }
      .el-button{
        display: block;
        width: 100%;
        margin-left: 0;
        padding: 13px 20px;
        font-size: 16px;
      }
    }
  }
  .selectTag{
    .tit{
      color: @color1;
      border-bottom: 1px solid #CCCCCC;
      text-align: center;
      overflow: hidden;
      margin: 0 27px;
      h2{
        font-size:20px;
        line-height: 20px;
        margin: 22px 0 11px;
      }
      h3{
        font-size:14px;
        line-height: 14px;
        margin-bottom: 25px;
      }
    }
    ul{
      padding-top:32px;
      overflow: hidden; 
      margin: 0 27px;
      li{
        border-radius:10px;
        margin-bottom: 22px;
        border:1px solid @color1;
        width: 114px;
        height: 114px;
        line-height: 114px;
        text-align: center;
        font-size:20px;
        color: @color1;
        margin-right: 18px;
        float: left;
        cursor: pointer;
        &.cur{
          background: @color1;
          color: #fff;
        }
      }
      li:nth-child(3n){
        margin-right: 0;
      }
    }
    .tip{
      height: 70px;
      background: url('./assets/images/tip.png') no-repeat;
      background-size: cover; 
      margin-bottom: 20px;
    }
    .btn{
      width: 200px;
      margin: 0 auto;
      .el-button{
        font-size: 16px;
        padding: 13px 20px;
      }
    }
  }
}
</style>



