<template>
  <!-- 轮播 + 分类-->
  <div class="banner">
    <!-- 轮播 -->
    <template>
      <el-carousel trigger="click" arrow='never' height="450px" width="100%" :interval="4000">
        <el-carousel-item v-for="(item,i) in slideList" :key="i">
          <div class='item' :style="{background: 'url('+item.imagepath+') no-repeat center'}">
            <!-- "urltype": "跳转 类型（1：课程id 2：老师id 3：其他）", -->
            <template v-if = 'item.urltype == 1'>
              <router-link :to="{path:'courseDetails',query: {id:item.courseid}}"></router-link>
            </template>
            <template v-if = 'item.urltype == 2'>
              <router-link :to="{path:'teacherDetails',query: {id:item.teacherid}}"></router-link>
            </template>
            <template v-if = 'item.urltype == 3'>
              <a :href="item.url"></a>
            </template>
          </div>
        </el-carousel-item>
      </el-carousel>
    </template>
  </div>
</template>
<script>
import Server from "@/service/request";
import Path from "@/service/Path";
export default {
  data(){
    return {
      slideList: [],
    }
  },
  created() {
    this.getSliderData();
  },
  methods: {
    // 轮播组建
    getSliderData() {
      Server.post(
        Path.getSlideList,
        {},
        res => {
          let { code, data } = res;
          if (code === 0) {
            this.slideList = data;
          }
        }
      );
    },
  }
}
</script>
<style lang="less" scoped>
@import "~@/assets/css/base";
// 轮播
.banner {
  position: relative;
  height: 450px;
  overflow: hidden;
  .item{
    height: 450px;
    background-size: cover !important;
    a{
      display: block;
      width: 100%;
      height: 100%;
    }
  }
  .el-carousel img{
    width: 100%;
    height: 100%;
    display: block;
  }
}
</style>
<style>
.el-carousel__indicators{
    bottom: 15px;
}
.el-carousel__button{
    width: 7px;
    height: 7px;
    border-radius: 50%;
    margin-right: 13px;
}
</style>


