<!--返回头部-->
<template>
  <ul v-show="backTopShow">
    <!-- <li class='list1'>
    <el-popover
      placement="left-start"
      width="60"
      trigger="hover"
      >
      <p class='ewm'><img src='./assets/images/ewm.png' /></p>
      <el-button slot="reference"><i></i> app下载</el-button>
    </el-popover>
    </li>
    <li class='list2'><i></i>在线咨询</li>
    <li class='list3'>
      <el-popover
        placement="left-start"
        width="140"
        trigger="hover"
        >
        <p class='tel'>400-800-8080</p>
        <el-button slot="reference"><i></i> 电话咨询</el-button>
      </el-popover>
    </li> -->
    <li class="back-top list4" @click="backTop()" ><i></i>返回顶部</li>
  </ul>
</template>

<script type="text/ecmascript-6">
  export default {
    data() {
      return {
        backTopShow:false
      }
    },
    components: {},
    mounted() {
      window.addEventListener('scroll', this.handleScroll)
    },
    methods: {
      backTop() {
        let back = setInterval(() => {
          if(document.body.scrollTop||document.documentElement.scrollTop){
            document.body.scrollTop-=100;
            document.documentElement.scrollTop-=100;
          }else {
            clearInterval(back)
          }
        },10);
      },
      handleScroll(){
        // if (document.documentElement.scrollTop + document.body.scrollTop > 100) 
        if (document.documentElement.scrollTop > 100) {
          this.backTopShow=true;
        }
        else {
          this.backTopShow=false;
        }
      }
    }
  }
</script>

<style lang="less" scoped>
.el-button{
  padding: 0;
  border: none;
  transition: initial
}
.el-button:focus, .el-button:hover{
  background: #333333;
  color: #fff;
}
.ewm{
  img{
    display: block;
    width: 60px;
    height: 60px;
  }
}
ul{
  position: fixed;
  right: 0;
  bottom: 100px;
  z-index: 2222;
  background: #fff;
  // border: 1px solid #eee;
  li{
    width: 58px;
    height:62px;
    padding: 5px 0 0 ;
    text-align: center;
    font-size: 12px;
    color: #666;
    cursor: pointer;
    display: block;
    transition: all .3s;
    box-sizing: border-box;
    i{
      width:29px;
      height:29px;
      display: block;
      margin: 0 auto 2px;
    }
    &:hover{
      background: #333333;
      color: #fff;
     .el-button{
        background: #333333;
        color: #fff;
      }
      i{
        background: url('./assets/images/top-h.png') center no-repeat #31465B;
      }
    }
    &.list1{
      i{
        background: url('./assets/images/icon2.png') center no-repeat ;
      }
    }
    &.list2{
      i{
        background: url('./assets/images/icon2.png') center no-repeat ;
      }
    }
    &.list3{
      i{
        background: url('./assets/images/icon3.png') center no-repeat ;
      }
    }
    &.list4{
      i{
        background: url('./assets/images/icon4.png') center no-repeat ;
      }
    }
  }
  li+li{
    border-top: 1px solid #f7f7f7;
  }
}

</style>


