<template>
    <div class="wrapper">
        <Head2 isTitleShow = 'true' title ='找回密码' isHotLine = 'true' ref="child"></Head2>
        <section class='container bg-white mt-30 mb-30' :style="{height: fullHeight-332+'px'}" style='min-height: 500px'>
            <div class='forget-wrapper'>
                <el-form v-if = 'isE' :model="form" :rules="formRules" ref="form" key='1'>
                    <Step step='1' :desc="['输入手机号','修改密码','完成']" class='step'/>
                    <el-form-item  prop="phone" class='h-no-border-area'>
                        <v-areaCode @selectValue = 'selectValue'></v-areaCode>
                        <!-- 请输手机号 -->
                        <el-input v-model="form.phone" :placeholder="$t('Main.Login.loginPhone')" class='pdleft55' autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item prop="imgcode" style='positive:relative'>
                        <!-- 请输入验证码 -->
                        <el-input v-model="form.imgcode" class='code-input' :placeholder="$t('Main.Login.codeinput')" autocomplete="off"></el-input>
                        <img  :src="CodeImg" alt="" @click="getimgCode()" class='code-img'/>
                        <!-- 换一张 -->
                        <!-- <span @click="getimgCode()" class='code-btn'>{{$t('Main.Forgetpwd.ChangeCode')}}</span>     -->
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="submitForm('form')" class='nextbtn'>下一步</el-button>
                    </el-form-item>
                </el-form>

                <el-form  v-if='isSuccess' :model="form2" :rules="formRules2" ref="form2" key='2'>
                    <Step step='2' :desc="['输入手机号','修改密码','完成']" class='step'/>
                    <!-- 短信验证码已发送至 -->
                    <h3>{{$t('Main.Login.sms')}}<span>{{form.phone}}</span></h3>
                    <el-form-item  prop="sms" class='mes-code'>
                        <!-- 请输入短信验证码 -->
                        <el-input v-model="form2.sms" :placeholder="$t('Main.Login.resMesCode')" autocomplete="off"></el-input>
                        <!-- 秒后重新获取 -->
                        <p class='time' v-if="getsms">
                            <template v-if = 'this.$i18n.locale == "zh-cn"'>
                                {{count}}{{$t('Main.Login.getsms')}}
                            </template>
                            <template v-else>
                                {{count}} {{$t('Main.Login.getsms2')}}
                            </template>
                        </p>
                        <!-- 获取短信验证码 -->
                        <p  class='time' v-else @click="codeBoth2()">{{$t('Main.Login.codeBoth')}}</p>
                    </el-form-item>
                    <el-form-item prop="newpwd">
                        <!-- 请输入6-16位新密码，区分大小写，不能使用空格 -->
                        <el-input type="password" v-model="form2.newpwd" :placeholder="$t('Main.Login.newpwd')" autocomplete="off"></el-input> 
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="submitForm2('form2')" class='nextbtn'>下一步</el-button>
                    </el-form-item>
                </el-form>

                <el-form  v-if='!isSuccess && !isE'>
                    <Step step='3' :desc="['输入手机号','修改密码','完成']" class='step'/>
                    <div class='success-wrapper tc'>
                        <div class="order-icon success"></div>
                        <h4>密码修改成功</h4>
                        <p>请重新登录</p>
                    </div>
                     <el-form-item>
                        <router-link to="/" class='nextbtn'>返回首页</router-link>
                    </el-form-item>
                </el-form>
            </div>
        </section>
        <Feet></Feet>
    </div>
</template>
<script>
import Head2 from '@/component/header2'
import Feet from '@/component/feet'
import Step from "@/component/step";
import Server from "@/service/request";
import Path from "@/service/Path";
import areaCode from '@/component/areaCode'
export default {
    data(){
        var validatePass = (rule, value, callback) => {
            var PasswordRegex = /^[a-zA-Z0-9_]{5,15}$/;
            if (!PasswordRegex.test(value)) {
                // 请输入6-16位新密码,字母、数字、下划线
                callback(new Error(this.$t('Main.Login.PasswordRegex')))
            } else {
                callback();
            }
        };
        var validatePhone = (rule, value, callback) => {
            var MobileRegex = /^1\d{5,14}$/;
            if (!MobileRegex.test(value)) {
                // 请输入正确的11位手机号码
                callback(new Error(this.$t('Main.Login.MobileRegex')))
            } else {
                callback();
            }
        };
        var validateMes = (rule, value, callback) => {
            var MesRegex = /^(\d){6}$/;
            if (!MesRegex.test(value)) {
                // 请输入正确的6位验证码
                callback(new Error(this.$t('Main.Login.MesRegex')))
            } else {
                callback();
            }
        };
        var validateCode = (rule, value, callback) => {
            var ImgRegex = /^[A-Za-z0-9]{5}$/;
            if (!ImgRegex.test(value)) {
                // 请输入正确的5位验证码
                callback(new Error(this.$t('Main.Login.ImgRegex')))
            } else {
                callback();
            }
        };
        return{
            isSelectValue: '86', //获取区号
            CodeImg:null, //图形验证码-图片
            sessionid:"",//图形验证码-id            
            count:"",//60s计时器

            isE:true,
            isSuccess:false,
            smsshow:false,
            timer:null,
            getsms:true,
            code:"",
            fullHeight :'',

            form: {
                imgcode: '',//图形验证码-数字
                phone:'',//手机号
            },
            formRules: {
                imgcode: [
                    // 请输入验证码
                    {required: true, message: this.$t('Main.Login.codeinput'),},
                    // { validator: validateCode, trigger: 'blur' }
                ],
                phone: [
                    // 请输入手机号码
                    {required: true, message: this.$t('Main.Login.loginPhone'), },
                    { validator: validatePhone, trigger: 'blur' }
                ],
            },
            form2: {
                sms:"", //短信验证码
                newpwd:"", //密码
            },
            formRules2: {
                sms: [
                    // 请输入短信验证码
                    {required: true, message: this.$t('Main.Login.resMesCode'), },
                    { validator: validateMes, trigger: 'blur' }
                ],
                newpwd: [
                    // 请输入密码
                    {required: true, message: this.$t('Main.Login.loginpwd'), },
                    { validator: validatePass, trigger: 'blur' }
                ],
            }
        }
    },
    components:{
        Feet,
        Head2,
        Step,
        'v-areaCode':areaCode,
    },
    created(){
        this.getimgCode();
        this.handleResize();
    },
    methods:{
        //获取区号
        selectValue(val){
            this.isSelectValue = val;
        },
        //第一步提交
        submitForm: function (formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    // this.isE = false;
                    this.codeBoth2();//发送短信验证码
                } else {
                    return false;
                }
            });
        },
        //第二步提交
        submitForm2: function (formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.sure();//确认修改密码
                } else {
                    return false;
                }
            });
        },

        handleResize () {
            this.fullHeight = document.documentElement.clientHeight;
        },
        //获取图形验证码
        getimgCode(){
            Server.post(Path.randomCode,{
			},res=>{
                let {code,data}=res;
				if(code===0){
                    this.sessionid = data.sessionid
					this.CodeImg = data.codeimg
				}else{
                    this.$message.error(res.info);
                }
			})
        },
        //发送手机验证码
        codeBoth2(){
            this.form.sms = ""
            this.form.newpwd = ""
            this.smsshow = false;
            this.getsms = true;
            let {phone,imgcode} = this.form;
            Server.post(Path.sendMobileMsg,{
                'mobile':phone,
                'code':imgcode,
                organid:1,
                'sessionid':this.sessionid,
                'type':1, //"type": "业务类型 1找回密码 2 注册",
                'prphone':this.isSelectValue,
			},res=>{
                let {code,data}=res;
				if(code===0){
                    this.isE = false;
                    this.isSuccess = true;
                    this.getCode();
				}else{
                    this.isE = true;
                    this.form.imgcode = '';
                    this.$message.error(res.info);
                    this.getimgCode();
                }
			})
        },
        //60s后再发送
        getCode(){
            this.smsshow = false;
            this.getsms = true;
            const TIME_COUNT = 60;
            if (!this.timer) {
                this.count = TIME_COUNT;
                this.show = false;
                this.timer = setInterval(() => {
                if (this.count > 0 && this.count <= TIME_COUNT) {
                    this.count--;
                    } else {
                        this.show = true;
                        clearInterval(this.timer);
                        this.timer = null;
                        this.smsshow = true;
                        this.getsms = false;
                    }
                }, 1000)
            }
        },
        //修改密码
        sure(){
            Server.post(Path.updatePass,{
				organid:1,
                mobile:this.form.phone,
                code:this.form2.sms,
                newpass:this.form2.newpwd
			},res=>{
                let {code,data}=res;
				if(code===0){
                    this.isSuccess = false;
				}else{
                    this.form2.sms = '';
                    this.form2.newpwd = '';
                    this.$message.error(res.info);
                }
			})
        },
    },
}
</script>
<style scoped lang="less">
@import "./assets/css/Forgetpwd.less";
</style>




