<!--微信支付-->
<template>
    <div class="wrapper">
        <div class="wxhead">
            <div class="container">
                <div class="flex-box">
                    <img :src="wxtitle" alt="">
                    <div class="segmentation"></div>
                    <!-- 微信支付 -->
                    <h2>
                        {{$t('Main.Micro.title')}}
                    </h2>
                </div>
            </div>
        </div>

        <div class="wxinfo">
            <div class="container">
                <ul>
                    <!-- 订单编号： -->
                    <li class="serialnumber">{{$t('Main.paymentCenter.ordernum')}}{{OrderDetailData.ordernum}}</li>
                    <!-- 订单类型：学生买课 -->
                    <li class="serialtype">{{$t('Main.Micro.serialtype')}}{{$t('Main.Micro.serialtypeName')}}</li>
                </ul>
                <div class="paymoney">
                    <!-- 应付金额： -->
                    {{$t('Main.paymentCenter.paymoney')}}<span>{{$t('Main.Common.symbol')}}{{paymoney}}</span>
                </div>
                <!-- 当前订单已失效，请重新下单 -->
                <p  v-if ='isTip' style='color:red; padding: 20px 0 0; clear:both'>
                    {{$t('Main.Micro.isTip')}}
                </p>
            </div>
        </div>

        <div class="paytitle">
            <div class="container">
                <div class="paycontent">
                    <ul class="flex-box">
                        <!-- 微信支付 -->
                        <li class="wxpay">{{$t('Main.Micro.title')}}</li>
                        <li><img :src="recommend" /></li>
                        <!-- 亿万用户的选择，更快更安全 -->
                        <li class="info">{{$t('Main.Micro.info')}}</li>
                    </ul>
                    <ul class="payimg">
                        <li class="qrcode"><img :src="codeurl" alt=""></li>
                        <li class="scanning"><img :src="payimg" alt=""></li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import wxtitle from './assets/images/wxpay.png'
import recommend from './assets/images/recommend.png'
import payimg from './assets/images/pay.png'
import Server from "@/service/request";
import Path from "@/service/Path";
export default {
    data(){
        return{
            wxtitle,
            recommend,
            payimg,
            codeurl:'',
            ordernum:'',
            price:0,
            OrderDetailData: {},//订单详情
            paymoney:'',
            isTip:false,
            buyClass:'',//购买2:课程 1:套餐
        }
    },
    mounted() {
        this.buyClass = this.$route.query.buyClass;
        this.showOrderDetail();
    },
    methods:{
        //获取订单详情
        showOrderDetail() {
            let path2 ;
            if(this.buyClass == 1){ //套餐
                path2 = Path.PackageshowOrderDetail
            }else if(this.buyClass == 2){//课程
                path2 = Path.showOrderDetail
            }
            Server.post(
                path2,
                {
                    ordernum: this.$route.query.order
                },
                res => {
                    let { code, data } = res;
                    if (code === 0) {
                        // debugger;
                        this.OrderDetailData = data; //所有的数据
                        
                        let discount = Number(this.$route.query.money);
                        let a = this.OrderDetailData.amount;
                        let b = this.OrderDetailData.balance;
                        if(discount == 0){
                            let c = a-b;
                            this.paymoney = c.toFixed(2);
                        }else{
                            let c = a-discount;
                            this.paymoney = c.toFixed(2);
                        }
                        this.geCode(); 
                    }
                }
            );
        },

        //获取支付二维码
        geCode() {
            let paytype = this.$route.query.paytype;
            let path3 ;
            if(this.buyClass == 1){ //套餐
                path3 = Path.PackagegotoPay
            }else if(this.buyClass == 2){//课程
                path3 = Path.gotoPay
            }
            Server.post(
                path3,
                {
                    ordernum: this.OrderDetailData.ordernum,
                    paytype:paytype,
                },
                res => {
                let { code, data } = res;
                if (code === 0) {
                    this.codeurl = data.codeurl;
                    this.setInt();
                }else{
                    this.$message.error(res.info);
                    // this.$router.push({
                    //     path: "/courseDetails",
                    //     query:{
                    //         id: this.OrderDetailData.schedulingid
                    //     }
                    // });
                }
            }
        )},
        //定时调用微信查询订单状态方法
        setInt () {
            let that = this;
            window.a = setInterval(function(){ 
                that.orderQuery();
            },3000)
        },
        //微信查询订单状态
        orderQuery() {
            Server.post(
                Path.orderQuery,
               {'ordernum':this.OrderDetailData.ordernum},
                res => {
                let { code, data } = res;
                if (code === 0) {
                    if(data.trade_state == 'SUCCESS'){//支付成功
                        clearInterval(window.a);
                        this.$router.push({
                            path:'/paymentSuccess',
                            query:{
                                type:0,
                                order: this.OrderDetailData.ordernum, 
                                buyClass:this.buyClass,
                            }
                        });
                        return false;
                    }else if (data.trade_state == 'PAYERROR'){ //支付失败
                        clearInterval(window.a);
                        this.$router.push({
                            path:'/paymentSuccess',
                            query:{
                                type:1,
                                order: this.OrderDetailData.ordernum, 
                                buyClass:this.buyClass,
                            }
                        });
                        return false;
                    }else if(data.trade_state == 'CLOSED' || data.trade_state == 'REVOKED'){//已关闭 已撤销
                        isTip = true;
                    }
                }else{
                    this.$message.error(res.info);
                }
            }
        );}
    }
}
</script>
<style scoped lang="less">
    .wxhead{
        width: 100%;
        min-width: 1300px;
        background: #fff;
        height: 60px;
        img{
            margin: 9px 0;
        }
        .segmentation{
            width:1px;
            height:20px;
            background:#676D70;
            margin: 20px 10px;
        }
        h2{
            font-size:16px;
            color:rgba(103,109,112,1);
            margin-top: 20px;
        }
    }
    .wxinfo{
        width: 100%;
        min-width: 1300px;
        padding-bottom: 30px;
        background: #eee;
        ul{
            float: left;
            li{
                font-size: 14px;
                color: rgba(103,109,112,1);
            }
            li.serialnumber{
                margin-top: 40px;
            }
            li.serialtype{
                margin-top: 20px;
            }
        }
        .paymoney{
            float: right;
            color: #676D70;
            font-size: 14px;
            margin-top: 51px;
            margin-right: 70px;
            span{
                color: #ff8208;
                font-size: 16px;
            }
        }
    }
    .paytitle{
        width: 100%;
        min-width: 1075px;
        height: 480px;
        // background: pink;
        margin-top: 30px;
        .paycontent{
            width: 100%;
            height: 40px;
            // background: yellow;
            line-height: 42px;
            ul.flex-box{
                border: 1px solid #999999;
                border-bottom: none;
                li.wxpay{
                    font-size: 14px;
                    font-family:PingFangSC-Medium;
                    color:rgba(51,51,51,1);
                    margin: 0 6px 0 26px;
                }
                img{
                    width: 46px;
                    height: 20px;
                    margin-top: 12px;
                }
                li.info{
                    font-size: 12px;
                    color: rgba(153,153,153,1);
                    margin-left: 16px;
                }
            }
            ul.payimg{
                border: 1px solid #999999;
                img{
                    display: block;
                }
                li.qrcode{
                    img{
                        width: 160px;
                        height: 160px;
                        margin: 95px auto 28px auto;
                    }
                }
                li.scanning{
                    cursor: pointer;
                    img{
                        width: 156px;
                        height: 52px;
                        margin: 0 auto 105px auto;
                    }
                }
            }
        }
    }
</style>



