<!--分页-->
<template>
  <ul class="pagination">
    <li :class="{'disabled': current == 1}">
      <a href="javascript:;" @click="setCurrent(current - 1)">
        <i class="el-icon-arrow-left"></i>
      </a>
    </li>
    <li v-for="(p,i) in grouplist" :key='i' :class="{'active': current == p.val}">
      <a href="javascript:;" @click="setCurrent(p.val)"> {{ p.text }} </a>
    </li>
    <li :class="{'disabled': current == page}">
      <a href="javascript:;" @click="setCurrent(current + 1)">
        <i class="el-icon-arrow-right"></i>
      </a>
    </li>
  </ul>
</template>

<script type="es6">
export default {
  data() {
    return {
      current: this.currentPage
    };
  },
  props: {
    total: {
      // 数据总条数
      type: Number,
      default: 0
    },
    limit: {
      // 每页显示条数
      type: Number,
      default: 10
    },
    currentPage: {
      // 当前页码
      type: Number,
      default: 1
    },
    pagegroup: {
      // 分页条数
      type: Number,
      default: 5,
      coerce: function(v) {
        v = v > 0 ? v : 5;
        return v % 2 === 1 ? v : v + 1;
      }
    }
  },
  computed: {
    page: function() {
      // 总页数
      return Math.ceil(this.total / this.limit);
    },
    grouplist: function() {
      // 获取分页页码
      var len = this.page,
        temp = [],
        list = [],
        count = Math.floor(this.pagegroup / 2),
        center = this.current;
      if (len <= this.pagegroup) {
        while (len--) {
          temp.push({ text: this.page - len, val: this.page - len });
        }
        return temp;
      }
      while (len--) {
        temp.push(this.page - len);
      }
      var idx = temp.indexOf(center);
      idx < count && (center = center + count - idx);
      this.current > this.page - count && (center = this.page - count);
      temp = temp.splice(center - count - 1, this.pagegroup);
      do {
        var t = temp.shift();
        list.push({
          text: t,
          val: t
        });
      } while (temp.length);
      if (this.page > this.pagegroup) {
        this.current > count + 1 &&
          list.unshift({ text: "...", val: list[0].val - 1 });
        this.current < this.page - count &&
          list.push({ text: "...", val: list[list.length - 1].val + 1 });
      }
      return list;
    }
  },
  methods: {
    setCurrent: function(idx) {
      if (this.current != idx && idx > 0 && idx < this.page + 1) {
        this.current = idx;
        this.$emit("pagechange", this.current);
      }
    }
  }
};
</script>

<style lang="less" scoped>
@import "~@/assets/css/base";
.pagination {
    overflow: hidden;
    display: table;
    margin: 50px auto 60px;
  li {
    height: 28px;
    float: left;
    color: @color1;
    margin: 0 5px;
    background: #fff;
    line-height: 29px;
    border-radius: 3px;
    border: 1px solid #DDE0E0;
    font-size: 12px;
    line-height: 28px;
    a {
      display: block;
      text-decoration: none;
      overflow: hidden;
      width: 100%;
      height: 100%;
      padding: 0 12px;
      text-decoration: none;
    }
    &.active,&:hover{
      background:@color1;
      border-color: @color1;
      color: #fff;
      a{
        color: #fff;
      }
    }
  }
}
</style>



