<!--空态图-->
<template>
  <div id="defaultData">
    <div class="default-img">
      <img src="./assets/images/data_default.png" alt="">
      <p class="fontColorr">{{text}}</p>
    </div>
  </div>
</template>
<script>
export default {
  name: "defaultData",
  props:{
    text:{
      default:'暂无数据',
      type: String,
    }
  },
  data(){
    return {

    }
  }
}
</script>
<style scoped>
  .default-img{
    text-align: center;
    width: 100%;
    padding: 100px 0;
  }
  .fontColorr{
    color:rgba(164,171,179,1);
    font-size: 16px;
    margin-top: 20px;
  }
</style>



