<template>
  <div>
	  <Head2 isTitleShow = 'true' title ='填写并确认订单' isStepShow = 'true' isStepNum = '1'></Head2>
		<div class="placeOrder container">
			<h1 class="orderTitle"> 订单详情</h1>
			<div class="topBox">
				<div class="logo">
					<img :src="courser.imageurl" @error = "_imageError"/>
				</div>
				<div class="OrderInfo">
					<table  border="0" cellpadding="0" cellspacing="0">
						<template v-if='courser.classtypes == 1'>
							<thead>
								<th>课程名称</th>
								<th width='20%'>授课老师</th>
								<th width='20%'>课程类型</th>
								<th width='20%'>课时数</th>
								<th width='15%'>价格/课时</th>
							</thead>
							<tbody>
								<td>{{courser.coursename}}</td>
								<td>{{courser.teachername}}</td>
								<td>{{courser.classtypes | changeType}}</td>
								<td>{{courser.periodnum}}</td>
								<td>{{$t('Main.Common.symbol')}}{{courser.totalprice}}</td>
							</tbody>
						</template>
						<template v-if='courser.classtypes == 2'>
							<thead>
								<th width='20%'>上课时间</th>
								<th>课程名称</th>
								<th width='15%'>授课老师</th>
								<th width='15%'>课程类型</th>
								<th width='15%'>课时数</th>
								<th width='15%'>价格/课时</th>
							</thead>
							<tbody>
								<td >
									{{courser.starttime}}<br>
									<template v-if='courser.starttime != courser.endtime'>{{courser.endtime}}</template>
								</td>
								<td>{{courser.coursename}}</td>
								<td>{{courser.teachername}}</td>
								<td>{{courser.classtypes | changeType}}</td>
								<td>{{courser.periodnum}}</td>
								<td>{{$t('Main.Common.symbol')}}{{courser.totalprice}}</td>
							</tbody>
						</template>
					</table>
				</div>
			</div>
			<div class="bottomBox">
				<div class='sendGift' v-if='courser.gift.giftstatus == 0 && courser.classtypes == 2'>
					<span>赠送教材</span>
					<label v-for ='(el,i) in courser.gift.giftdescribe' :key='i'>
							{{el.name}} x {{el.num}}
						</label>
				</div>
				
				<template v-if='userPackageList.length > 0 && Number(courser.totalprice) != 0'>
					<div class="couponBox">
						<p class="orderTitle left">抵扣课时</p>
						<!-- <router-link target="_blank" to="/concessionDescription" class="userCoupon">如何使用优惠卷？</router-link> -->
					</div>
					<ul class="couponList">
						<li
							v-for ='(item,i) in userPackageList' :key='i'>
							<p class="brushVoucher">
								<el-radio v-model="packageRadio" :label="i" @change='getPeriod(item)'>
									{{item.setmeal}}/{{item.total}}课时
									<template v-if='item.type==2'>
										(赠)
									</template>
								</el-radio>
							</p>
							<p class="endTime">截止时间：<span class="time">{{item.endtime | getTime}}</span></p>
							<p class="voucher"><span class="price">可抵扣{{item.surplus}}课时</span></p>
						</li>
					</ul>
				</template>

				<!-- 收货地址 -->
				<v-address @getAddressId = "getAddressId" v-if='courser.gift.giftstatus == 0 && courser.classtypes == 2'></v-address>

				<div class="contentBox">
					<!-- 请在30分钟内支付，逾期不支付，订单自动取消。 -->
					<p class="tip">
						<icon name="warnImg"  :width="20" :height="20" class="warnImg"></icon>
						<!-- <img :src="prompt" class="warnImg" /> -->
						{{$t('Main.placeOrder.tip')}}</p>
					<div class="classPrice">
						<!-- 课程总价： -->
						<p>{{$t('Main.paymentCenter.priceP')}} {{$t('Main.Common.symbol')}}{{originalCost}}</p>
						<!-- 实付价格： -->
						<p>{{$t('Main.paymentCenter.totalprice')}}<span class="price">{{$t('Main.Common.symbol')}}{{actuallyPrice}}</span></p>
						<!-- <p>
							<input type="checkbox" name="" id="" value=""  v-model="checked" />
							<router-link target="_blank" :to="{path:'/agreement'}">《用户购买协议》</router-link>
						</p> -->
					</div>
				</div>
				<div class="placeBox">
					<!-- 提交订单 -->
					<p class="orderBtn" @click="submissionOrder()">{{$t('Main.placeOrder.orderBtn')}}</p>
				</div>
			</div>
		</div>
		<Feet></Feet>
  </div>
</template>
<script>
import Server from "@/service/request";
import Path from "@/service/Path";
import Head2 from '@/component/header2'
import Feet from '@/component/feet'
import address from './address'
export default { 
	data(){
		return{
			// checked:false,
			ordernum:'',//订单号
			courser:{}, //课程数据
			userPackageList:[],//套餐列表
			packageRadio:-1,
			actuallyPrice:'',//实付价格
			originalCost:'',//课程原价
			subjoin:{
				addressid:'',//地址id
				type:'',//套餐类型 1购买2赠品
				packageid:'', //"套餐id"
			 	packagegiftid:'',//"赠送套餐id"
				packageuseid:'',//套餐使用id 
			}
		}
	},
	components: {
		Head2,
		Feet,
		'v-address':address
	},
	created(){
		//获取课程详情
		// this.getCurriculumInfo();
		this.getUserPackage();
		this.courser = JSON.parse(localStorage.getItem('classinfo'));
		this.originalCost = (this.courser.totalprice * this.courser.periodnum).toFixed(2)//总价
		this.actuallyPrice = this.originalCost //实付价格
		this.getAddressList();
	},
	methods:{
		//优惠券页面
	  userCoupon(){
      this.$router.push({
				path:'/concessionDescription'
			})
		},
		//获取优惠券列表
		getUserPackage() {
			Server.post(
				Path.getUserPackage, {
					curriculumid: this.$route.query.id,
					'amount':'',
				},
				res => {
					let {
						code, data
					} = res;
					if(code === 0) {
						this.userPackageList = data; //获取优惠券列表
					}
				}
			);
		},
		// 获取课时数抵扣
		getPeriod(val) {
			let {originalCost,courser} = this ;
			this.actuallyPrice = originalCost //总价;
			this.subjoin.type = val.type;
			this.subjoin.packageid = val.packageid;
			this.subjoin.packagegiftid = val.packagegiftid || '';
			this.subjoin.packageuseid = val.packageuseid || '';

			let unitPrice = ''; //单价
			unitPrice = (originalCost / courser.periodnum).toFixed(2)
			
			//课程课时数 > 套餐可使用数
			if(courser.periodnum >= val.surplus){
				this.actuallyPrice = (originalCost-(Number(courser.totalprice)*val.surplus)).toFixed(2)
			}else{
				this.actuallyPrice = (originalCost-(Number(courser.totalprice)*courser.periodnum)).toFixed(2)
			}
		},

		// 获取当前收货地址id
		getAddressId(val) {
			this.subjoin.addressid = val;
		},
		// 查询收货地址
		getAddressList() {
			Server.post(
				Path.getAddressList, {
				},
				res => {
					let {
						code, data
					} = res;
					if(code === 0) {
						this.addressList = data; 
						this.ruleForm = data[0];
						data.map((el,i) => {
							if(el.isdefault == 1){
								this.subjoin.addressid = data[i].id;
							}else{
								this.subjoin.addressid = data[0].id;
							}
						})
					}
				}
			);
		},
		//获取课程详情
		getCurriculumInfo() {
			Server.post(
				Path.getCurriculumInfo, {
					scheduid: this.$route.query.id,
				},
				res => {
					let {
						code, data
					} = res;
					if(code === 0) {
						this.courser = data.courser; //课程相关信息列表
						this.actuallyPrice = data.courser.actuallyPrice
					}
				}
			);
		},
		//提交订单
	  submissionOrder(){
		  // if(this.checked==false){
			// this.$message.warning('请签定用户购买协议');
			// return false ;
			// }
			let{courser,actuallyPrice,originalCost,subjoin,packageRadio} = this;
			// console.log(1,subjoin.addressid)
			if(subjoin.addressid == '' && courser.gift.giftstatus == 0 && courser.classtypes == 2){
				this.$message.error('请选择收货地址');
				return false ;
			}
			Server.post(Path.gotoOrder,{
				'courseid':courser.curriculumid || courser.courseid, // "课程id",
				'studentid':this.$loginInfo.uid,  //学生登陆用户名id
				'schedulingid':courser.scheduid || '', //班级排课id
				'amount':actuallyPrice,  //实付金额
				'ordersource':1, //下单渠道 1web 2app
				'originprice':originalCost, //课程总价
				'addressid':subjoin.addressid, //"地址id",
				'usestatus':packageRadio>=0?1:0, // "是否使用优惠券0未使用1使用",
				'type':subjoin.type, //"套餐类型 ",
				'packageid':subjoin.packageid, //"套餐id"
			  'packagegiftid':subjoin.packagegiftid,//"赠送套餐id"
				'packageuseid':subjoin.packageuseid,//套餐使用Id
			},res=>{
				let {code,data}=res;
				if(code===0){
					this.ordernum=data.ordernum
					this.$router.push({
						path:'/paymentCenter',
						query:{
							ordernum:this.ordernum
						}
					})	
				}else{
					this.$message.error(res.info)
				}
			})	
		},
		// 两个数相除取2位小时
		parcentNum(num,num2) {
			return (Math.round(num / num2 * 10000) / 100.00 + "&")
		}
	},  
}
</script>
<style lang="less" scoped>
  @import './assets/css/placeOrder.less';
</style>




