<template>
  <div class="teacherContent">
	  <Head2 ref="child" :isCur='3' isNavShow = true isSeachShow = true isHotLine = 'true'></Head2>
		<panel-title :crumbs="[
      {
        name:'首页',
        route:'',
      },{
        name:'名师堂',
        route:'/teacher',
      },{
        name:'名师详情',
        route:'',
      }
    ]" />
		<div class="container">     
			<!--老师详情-->
			<div class="teacherInfo border-line mb-30">
				<div class="teacherInfoLeft"  v-if="picShow">
					<img :src="TeacherCurriculum.imageurl || defaultTeacher" alt=""/>
				</div>
				<div class="teacherInfoRight">
					<h2 class="name">
						{{TeacherCurriculum.teachername}} 
					</h2> 
					<div class='tag'>
						<div class='focus-btn' v-if = 'TeacherCurriculum.is_collect == 0' @click = 'teacherCollect'>
							<i></i>
							{{$t('Main.ClassInfo.Collect')}}
							<!-- 收藏 -->
						</div>
						<div class='focus-btn focus-btn2' v-if = 'TeacherCurriculum.is_collect == 1' @click = 'cancelTeacher'>
							<i></i>
							{{$t('Main.ClassInfo.CancelCollect')}}
							<!-- 取消收藏 -->
						</div>
						<!-- <icon name="mail"  :width="14" :height="14" style='color:#66b1e8'></icon>
						<span>好评</span>
						<p>{{TeacherCurriculum.score}}</p>-->
					</div>
					<p class="des"> 
						{{TeacherCurriculum.profile || $t('Main.teacherDetails.profile')}}
					</p>
				</div>
			</div>
		</div>

		<div class='container'>
			<!--左侧-->
			<div class="" >
				<!--免费课程-->
				<div class="classBox bg-white mb-20">
					<h2 class="classname">
						<span class='left'>免费课程</span>
					</h2>
					<courseList2 :Courser='classInfo0' :teacherId='$route.query.id' v-if='classInfo0.length>0 && classInfo0.length<5'></courseList2>
					<courseList2 :Courser='classInfo0' :teacherId='$route.query.id' v-if='classInfo0.length>4' :type='1'></courseList2>
					<template v-if='classInfo0.length ==0'>
						<p style='padding: 0px 60px 30px;font-size: 14px;'>暂无数据</p>
					</template>
				</div>

				<!-- 在售课程 -->
				<div class="classBox bg-white mb-30">
					<h2 class="classname">
						<span class='left'>在售课程</span>
					</h2>
					<courseList2 :Courser='classInfo1' :teacherId='$route.query.id' v-if='classInfo1.length !=0'></courseList2>
					<template v-if='classInfo1.length ==0'>
						<p style='padding: 0px 60px 30px;font-size: 14px;'>暂无数据</p>
					</template>
				</div>

				<!--分页-->
				<!-- <v-pagination :total="total" :current-page='current' :limit="limit" @pagechange="pagechange" v-if = 'total!= 0'></v-pagination> -->

				<!--他的评价-->
				<div class="classBox bg-white border-line mb-20" style='display:none'>  
					<h2 class="classname border-bottom-line">
						<!-- 他的评价 -->
						<span class='left'>{{$t('Main.teacherDetails.evaluateTitle')}}</span>
						<!-- 综合评分： -->
						<span class='right'>
							<label style='vertical-align: middle;'>	{{$t('Main.teacherDetails.score')}}{{$t('Main.Common.colon')}}</label>
							<el-rate
							v-model="TeacherCurriculum.score*1"
							disabled
							show-score
							text-color="#ff9900"
							score-template="{value}"
							style='display: inline-block;'>
							</el-rate>
						</span>
					</h2>   
					<ul class="classItems" @click="getHeight()">
						<template v-if = 'CommentList.length > 0'> 
							<li class='item item2 item3 item4' v-for="item in CommentList" :key='item.teacherid'>
								<div class="personImgBox">
									<img :src="item.imageurl" alt="item.nickname" class="Img" @error = "_imageError"/>
								</div>
								<div class="descBox descBox2 descBox3">
									<div class='titleBox'>
										<h2 class="title left">{{item.nickname}}</h2>
										<p class="classType classType3 right">
											<span>
												<icon name="time"  :width="16" :height="16" class="timeImg"></icon>
												<!-- <img src="./assets/images/time.png" class="timeImg" alt=""> -->
												{{item.addtime}}
											</span>
											<span>
												<icon name="score"  :width="16" :height="16" class="timeImg"></icon>
												<!-- <img src="./assets/images/score.png" class="scoreImg"  alt=""> -->
												{{item.score}}</span>
										</p>
									</div>
									<p class="msg">
										{{item.content}}
									</p>
								</div>
							</li>
						</template>
						<defaultShow v-else :text="$t('Main.ClassInfo.noData')"/>
					</ul> 
				</div>
			</div>
		</div>
		<Feet></Feet>
  </div>
</template>
<script>
import panelTitle from "@/component/panelTitle";
import Server from "@/service/request";
import Path from "@/service/Path";
import Page from '@/component/page'
import defaultShow from "@/component/defaultShow";
import Head2 from "@/component/header2";
import Feet from '@/component/feet'
import boyImg from "./assets/images/men.png";
import girlImg from "./assets/images/women.png";
import defaultTeacher from "@/assets/images/defaultTeacher.png";
import courseList2 from "@/component/courselist2";
export default {
  components:{
		panelTitle,
		Head2,
		'v-pagination': Page,
		Feet,
		defaultShow,
		courseList2,
  },
  data(){
    return{
      TeacherCurriculum: {
				lable:[],
			},//老师个人信息

      classInfo0: [],//免费课程
      classInfo1: [],//在售课程
			CommentList: [],//他的评价
		
			picShow:false,
			total: 0,     // 记录总条数
			limit: 20,   // 每页显示条数
			current: 1,   // 当前的页数

			boyImg:boyImg,
			girlImg:girlImg,
			defaultTeacher,
			isshowsex:false
    }
  },
	created(){
		this.getCategoryList(); //老师详情
		this.getTeacherClass(0); //他的班级
		this.getTeacherClass(1); //他的班级
    // this.getCommentList(this.current); //评价
	},
	methods:{
		//老师详情-老师个人信息
    getCategoryList() {
      Server.post(
        Path.getTeacherCurriculum,
        {
          teacherid: this.$route.query.id
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
						this.TeacherCurriculum = data;
						let sex = data.sex;
						if(sex == 0){
							this.isshowsex = false
						}else{
							this.isshowsex = true
						}
          }
        }
      );
		},
		//老师详情-他的班级
    getTeacherClass(type) {
      Server.post(
				/* "classtype": "课程类型  0免费课程1在售课程"*/ 
        Path.getTeacherClass,
        {
					teacherid: this.$route.query.id,
					'classtype':type
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
						if(type == 0){
							this.classInfo0 = data.data;
						}else if(type == 1){
							this.classInfo1 = data.data;
						}
          }
        }
      );
    },
		//分页
		pagechange(currentPage) {
			this.getCommentList(currentPage);
		},
		//评价
    getCommentList(currentPage) {
      Server.post(
        Path.getCommentList,
        {
          teacherid: this.$route.query.id,
          pagenum: currentPage,
          limit: this.limit
        },
        res => {
          let { code, data } = res;
          if (code === 0) {
            this.CommentList = data.data;
           	this.total = data.pageinfo.total;
						this.current = Number(data.pageinfo.pagenum);
						this.limit = Number(data.pageinfo.pagesize);
          }
        }
      );
    },
		//收藏老师
		teacherCollect() {
			if(this.$loginInfo == null || this.$loginInfo ==''){
				this.$refs.child.goLogin(0);
				return false;
			}
			Server.post(
				Path.teacherCollect, {
					teacherid: this.$route.query.id,
					studentid:this.$loginInfo.uid,
				},
				res => {
					let { code, data } = res;
					if(code === 0) {
						this.$message.success(res.info);
						this.getCategoryList();//获取老师详情数据
					}else{
						this.$message.error(res.info);
					}
				}
			);
		},
		//取消收藏老师
		cancelTeacher() {
			if(this.$loginInfo == null || this.$loginInfo ==''){
				this.$refs.child.goLogin(0);
				return false;
			}
			Server.post(
				Path.cancelTeacherCollect, {
					teacherid: this.$route.query.id,
					studentid:this.$loginInfo.uid,
				},
				res => {
					let { code, data } = res;
					if(code === 0) {
						this.$message.success(res.info);
						this.getCategoryList();//获取老师详情数据
					}else{
						this.$message.error(res.info);
					}
				}
			);
		},
	}, 
	watch:{
		TeacherCurriculum(val, oldVal){
			if(val.addtime){
				this.picShow=true;
			}
		}
	}
}
</script>

<style lang="less" scoped>
@import "~@/assets/css/base";
@import "~@/assets/css/list";
//标题
.classname{
	padding: 24px 30px;
	font-size: 16px;
	color: #333;
	overflow: hidden;
	span{
		display: inline-block;
		line-height: 24px;
		border-left:4px solid @color1; 
		padding-left: 26px;
		font-size: 22px;
		color: @color1;
	}
}
.focus-btn{
	display: inline-block;
	height: 20px;
	line-height: 20px;
	font-size: 14px;
	color: #666;
	cursor: pointer;
	margin-bottom: 15px;
	transition: all 0.4s ease;
	&:hover{
		color: @color1;
	}
	i{
		width: 18px;
		height: 18px;
		display: inline-block;
		vertical-align: middle;
		margin-top: -2px;
		margin-right: 3px;
		background: url('./assets/images/favorite.png') center no-repeat;
	}
}
.focus-btn2{
  i{
		background: url('./assets/images/favorite-cur.png') center no-repeat;
	}
}

//老师详情
.classItems{
	background: #fff;
}
.teacherInfo{
	background: #fff;
	padding: 55px 84px 62px 84px;
	overflow: hidden;
	.teacherInfoLeft{
		width: 260px;
		height: 260px;
		border-radius: 50%;
		background: #ccc;
		float: left;
		overflow: hidden;
		img{
			display: block;
			height: 100%;
			width: 100%;
			border-radius: 50%;
		}
	}
	.teacherInfoRight{
		margin-left: 330px;
		position: relative;
		.name{
			font-size: 23px;
			line-height: 30px;
			margin: 15px 0 0px;
			color: #1A1A1A;
			overflow: hidden;
		}
		.tag{
			height: 18px;
			margin: 50px 0 ;
			span{
				margin: 0 30px 0 10px;
				display: inline-block;
				color: #666;
			}
			p{
				display: inline-block;
				color: #41B1E6;
				font-size:14px;
			}
		}
		.des{
			font-size: 14px;
			color: #1A1A1A;
			line-height: 26px;
			max-height: 104px;
			overflow: hidden;
			word-wrap: break-word;
		}	
	}
}
.timeImg{
  margin-right: 5px;
}
.classBox{
	overflow: hidden;
}
</style>




