import Vue from 'vue' 
import Router from 'vue-router'
import Forgetpwd from '@/container/Forgetpwd'
import concessionDescription from '@/container/ConcessionDescription'
import courseDetails from '@/container/courseDetails'
import paymentCenter from '@/container/paymentCenter'
import paymentSuccess from '@/container/paymentSuccess'
import placeOrder from '@/container/placeOrder'
import teacherDetails from '@/container/teacherDetails'
import Agreement from '@/container/Agreement'
import Home from '@/container/Home'
import Classpage from '@/container/Classpage'
import Searchpage from '@/container/Searchpage'
import Micro from '@/container/Micro'
import gywm from '@/container/gywm' //关于我们
import download from '@/container/download' //下载
import teacher from '@/container/teacher' //名师堂
import combo from '@/container/combo' //课时套餐
import comboDetails from '@/container/comboDetails' //课时套餐-详情
import comboPlaceOrder from '@/container/comboPlaceOrder' //课时套餐-提交订单第一步
import comboPaymentCenter from '@/container/comboPaymentCenter' //课时套餐-提交订单第二步

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    //首页
    {
      path: '/Home',
      name: 'Home',
      component: Home
    },
    //微信支付
    {
      path: '/micro',
      name: 'micro',
      component: Micro
    },
    //搜索页
    {
      path: '/Searchpage',
      name: 'Searchpage',
      component: Searchpage
    },
    //列表页
    {
      path: '/Classpage',
      name: 'Classpage',
      component: Classpage
    },
    //忘记密码
    {
      path: '/forgetpwd',
      name: 'Forgetpwd',
      component: Forgetpwd
    },
    //优惠券说明
    {
      path:'/concessionDescription',
      name:'concessionDescription',
      component:concessionDescription
    },
    //课程详情
    {
      path:'/courseDetails',
      name:'courseDetails',
      component:courseDetails
    },
    //老师详情
    {
      path: '/teacherDetails',
      name: 'teacherDetails',
      component: teacherDetails
    },
    //支付中心
    {
      path:'/paymentCenter',
      name:'paymentCenter',
      component:paymentCenter
    },
    //提交订单
    {
      path:'/placeOrder',
      name:'placeOrder',
      component:placeOrder
    },
    //支付成功
    {
      path: '/paymentSuccess',
      name: 'paymentSuccess',
      component: paymentSuccess
    },
    //用户协议
    {
      path: '/agreement',
      name: 'Agreement',
      component: Agreement
    },
    //关于我们
    {
      path: '/gywm',
      name: 'gywm',
      component: gywm
    },
    //下载
    {
      path: '/download',
      name: 'download',
      component: download
    },
    //名师堂
    {
      path: '/teacher',
      name: 'teacher',
      component: teacher
    },
    //课时套餐
    {
      path: '/combo',
      name: 'combo',
      component: combo
    },
    //套餐详情
    {
      path: '/comboDetails',
      name: 'comboDetails',
      component: comboDetails
    },
    //课时套餐-提交订单第一步
    {
      path: '/comboPlaceOrder',
      name: 'comboPlaceOrder',
      component: comboPlaceOrder
    },
    //课时套餐-提交订单第二步
    {
      path: '/comboPaymentCenter',
      name: 'comboPaymentCenter',
      component: comboPaymentCenter
    },
  ],
  scrollBehavior(to, from, savedPosition) {
    return {
      x: 0,
      y: 0
    }
  }
})



