<!-- 课程列表 横向整个-->
<template>
  <div>
    <template v-if = 'filterCourserList.length > 0'>
      <ul class="classBox">
        <!-- 老师列表 -->
        <template v-if = 'searchtypenum == 2'>
          <li class='item item3' v-for="(item,index) in filterCourserList" :key='index'>
            <div class="classImgBox">
              <img :src="item.imageurl || defaultTeacher" :alt='item.coursename' class="Img" @error = "_imageError"/>
            </div>
            <div class="descBox">
              <h2 class="title">{{item.teachername}}</h2>
              <p class="msg">{{item.profile}}</p>
              <!-- 查看详情 -->
              <router-link :to="{path:'teacherDetails',query: {id:item.teacherid}}" class="see">{{$t('Main.ClassInfo.see')}}</router-link>
            </div>
          </li>
        </template>
        <!-- 课程列表 -->
        <template v-else>
          <li class='item' v-for="(item,index) in filterCourserList" :key='index'>
            <div class="classImgBox">
              <img :src="item.imageurl" :alt='item.coursename' class="Img" @error = "_imageError"/>
            </div>
            <div class="descBox">
                <h2 class="title line1">{{item.coursename}}</h2>
                <p class="msg">{{item.subhead}}</p>
                <p class="classType">
                  <span>
                    <label>{{item.classtypes | changeType}}</label>
                  </span>
                </p>
                <p class="price">
                  <span>{{$t('Main.Common.symbol')}}</span><!-- 录播 --><template v-if = 'item.classtypes == 1 || (item.classtypes == 2 && item.price == item.maxprice)'>{{item.price}}</template><!-- 直播 --><template v-else>{{item.price}}-{{item.maxprice}}</template><span>/课时</span>
                </p>
              <!-- 查看详情 -->
              <router-link :to="{path:'courseDetails',query: {id:item.courseid,type:Number(item.classtypes)}}" class="see">{{$t('Main.ClassInfo.see')}}</router-link>
            </div>
          </li>
        </template>
      </ul>
    </template>
    <defaultShow v-else :text="$t('Main.ClassInfo.noData')"></defaultShow>
  </div>
</template>
<script>
import defaultTeacher from "@/assets/images/defaultTeacher.png";
import defaultShow from "@/component/defaultShow";
export default {
  name: "",
  /*
    *searchtypenum:1-列表页课程，2-搜索页课程，3-老师详情页的我的班级
    *searchtypenum:2-老师列表，
  */
  props:['filterCourserList','searchtypenum'],
  components: {
    defaultShow,
  },
  data(){
    return{
      defaultTeacher
    }
  }
}
</script>
<style scoped lang="less">
  @import "~@/assets/css/list2";
</style>



