<template>
  <div class="section">
    <div class='container'>
      <h2 class='tc'>「如何<b>免费体验</b>」柠檬课程</h2>
      <ul class='flex-box step'>
        <li class="list flex1">
          <em><img src='./assets/images/icon1.png' alt=''/> </em>
          <h3>在线注册</h3> 
          <p>免费领取价值<span>298</span>元试听课</p>
        </li>
        <li class="list flex1">
          <em><img src='./assets/images/icon2.png' alt=''/></em>
          <h3>咨询预约</h3> 
          <p>确定试听课时间</p>
        </li>
        <li class="list flex1">
          <em><img src='./assets/images/icon3.png' alt=''/></em>
          <h3>开始上课</h3> 
          <p>下载应用，开始在线学习</p>
        </li>
      </ul> 
      <ul class='flex-box status'>
        <li class="list flex1">
          <h3>课前</h3> 
          <p>预习课程<br/>点读课件</p>
        </li>
        <li class="list flex1">
          <h3>课中</h3> 
          <p>小班上课<br/> 随堂测试<br/> 家长实时监测</p>
        </li>
        <li class="list flex1">
          <h3>课后</h3> 
          <p>老师点评<br/> 贴心反馈<br/> 课程精彩回放 </p>
        </li>
      </ul> 
      <div class='btn' @click="goDraw">申请免费试听课</div>
    </div>
    <!-- 申请免费试听课 -->
    <addreceiv ref="child"></addreceiv>
  </div>
</template>
<script>
import addreceiv  from '../addreceiv'
export default{
  components:{
    addreceiv,
  },
  methods:{
    // 申请免费试听课
    goDraw(){
      this.$refs.child.childFn()
    },
  },
}
</script>
<style lang="less" scoped>
@import "~@/assets/css/base";
.section{
  height: 754px;
  background: #fff;
  overflow: hidden;
  box-sizing: border-box;
  .container{
    height: 100%;
    position: relative;
    overflow: hidden;
  }
  h2{
    text-align: center;
    margin: 114px 0 83px;
    color: #2a333f;
    font-size: 30px;
    line-height: 30px;
    b{
      color: @color5;
    }
  }
  p.tit{
    font-size: 18px;
    line-height: 18px;
    color: #2a333f;
    text-align: center;
    margin-bottom: 70px;
    span{
      color: @color5;
    }
  }
  ul.step{
    width: 860px;
    li{
      position: relative;
      em{
        display: inline-block;
        width: 60px;
        height: 60px;
        float: left;
      }
      h3{
        font-size: 26px;
        color: #2A333F;
        line-height: 26px;
        margin-bottom: 14px;
        margin-left: 55px;
      }
      p{
        font-size: 16px;
        line-height: 16px;
        color: #2a333f;
        display: block;
        margin-left: 55px;
      }
      span{
        color: @color5;
      }
      &:nth-child(1):after,&:nth-child(2):after{
        content: '';
        display: block;
        overflow: hidden;
        width: 23px;
        height: 23px;
        background: url('./assets/images/right.png') no-repeat center;
        position: absolute;
        right: -14px;
        top: 50%;
        margin-top: -11.5px;
      }
      &:nth-child(2):after{
        right: 18px;
      }
      &:nth-child(2){
        em{
          margin-left: 40px;
        }
        h3{
          margin-left: 40px;
        }
      }
    }
  }
  ul.status{
    width: 1042px;
    height:226px;
    margin: 91px auto 0;
    position: relative;
    color: #ffffff;
    text-align: center;
    h3{
      font-size: 39px;
      line-height: 39px;
      margin-bottom: 40px;
      position: relative;
      margin: 40px 0 32px;
      &:after{
        content: '';
        display: block;
        overflow: hidden;
        width: 40px;
        height: 3px;
        background: @color5;
        position: absolute;
        left: 50%;
        bottom: -12px;
        margin-left: -20px;
      }
    }
    p{
      font-size:18px;
      line-height: 27px;
    }
    li{
      width: 226px;
      position: relative;
    }
    li:nth-child(1){
      background: url('./assets/images/icon7.png') no-repeat center;
    }
    li:nth-child(2){
      background: url('./assets/images/icon5.png') no-repeat center;
    }
    li:nth-child(3){
      background: url('./assets/images/icon6.png') no-repeat center;
    }
    li:nth-child(1):after,li:nth-child(2):after{
      content: '';
      display: block;
      overflow: hidden;
      width: 123px;
      height: 0;
      position: absolute;
      left: 286px;
      bottom: 50%;
      border-bottom: 2px dashed #b3cee1;
    }
  }
  .btn{
    position: absolute;
    top: 230px;
    right: 100px;
    line-height: 52px;
    border-radius: 8px;
    text-align: center;
    color: #fff;
    font-size: 24px;
    width: 222px;
    height: 52px;
    box-shadow: 2px 0px 2px rgba(13,17,17,0.24);
    background: @color5;
    transition: all .3s  ease-in-out;
    cursor: pointer;
    &:hover{
      background:@color6;
    }
  }
}
</style>


