<!--面包屑导航-->
<template>
  <div class="panelTitle container">
    <ul class="bread-crumbs">
      <router-link tag="li" v-for="(el, i) in crumbs" :key="i" :to="el.route">{{el.name}}</router-link>
    </ul>
  </div>
</template>
<script>
  export default {
    name: "panelTitle",
    props: {
      crumbs:{
        default:function(){
          return [
            {
              name:'一级',
              route:''
            }
          ]
        },
        type:Array
      }
    }
  }
</script>
<style scoped lang='less'>
@import "~@/assets/css/base";
  .panelTitle {
    font-size: 16px;
    line-height: 54px;
  }
  .bread-crumbs li {
    position: relative;
    display: inline-block;
    font-size: 15px;
    color: #8F8E90;
    cursor: initial;
  }
  .bread-crumbs li:last-of-type {
    color: #525866;
    pointer-events: none;
  }
  .bread-crumbs li:after {
    margin: 0px 6px 0;
    background: url('../assets/images/icon_right.png') no-repeat 2px 2px;
    content: '';
    display: inline-block;
    color: #A4ABB3;
    font-weight: bold;
    width: 20px;
    height: 20px;
    background-size: 73%;
    vertical-align: middle;
  }
  .bread-crumbs li:last-of-type::after{
    background-image: url('');

  }
  .bread-crumbs li:last-of-type:after {
    content: '';
  }
</style>



