<!-- 课程列表 横向单个-->
<template>
  <div class="content" :class="{'content2':type==2 ,'content3':type==3}">
    <template v-if='type!=1 && type!=2'>
      <div class="mediaBox  " v-for="(item,index) in Courser" :key = "index" @click="message(item.courseid,item.classtypes,teacherId)">
        <div class="media">
          <div class="mediaImg">
            <img :src="item.imageurl" alt="" @error = "_imageError"/>
          </div>
          <div class="mediaCon">
              <h3 class='line1'>{{item.coursename}}</h3>
              <div class="mediaInfo">
                <div class="mediaName left">{{item.classtypes | changeType}}</div>
                <div class="mediaPrice right line1">
                  {{$t('Main.Common.symbol')}}<!-- 录播 --><template v-if = 'item.classtypes == 1 || (item.classtypes == 2 && item.price == item.maxprice)'>{{item.price}}</template><!-- 直播 --><template v-else> {{item.price}}-{{item.maxprice}}</template><span>/课时</span>
                </div>
              </div>
          </div>
        </div>
      </div>
    </template>
    <template v-if='type==1 || type==2'>
      <div class="swiper-container"  >
        <div class="swiper-wrapper">
          <div  v-for="(item,index) in Courser" :key="index" class="swiper-slide">
            <router-link :to="{path:'courseDetails',query: {id:item.courseid,teacher:$route.query.id,type:Number(item.classtypes)}}" class="mediaBox">
              <div class="media">
                <div class="mediaImg">
                  <img :src="item.imageurl" alt="" @error = "_imageError"/>
                </div>
                <div class="mediaCon">
                  <h3 class='line1'>{{item.coursename}}</h3>
                  <div class="mediaInfo">
                    <div class="mediaName left">{{item.classtypes | changeType}}</div>
                    <div class="mediaPrice right line1">
                      {{$t('Main.Common.symbol')}}{{item.price}}<span>/课时</span>
                    </div>
                  </div>
                </div>
              </div>
            </router-link>
          </div>
        </div>
        <div class="swiper-pagination"></div>
      </div>
    </template> 
  </div>
</template>
<script>
import Swiper from 'swiper';
import 'swiper/dist/css/swiper.min.css';
export default {
  name: "",
  props:['Courser','type','teacherId'],
  created() {
    this.$nextTick(() => {
      new Swiper('.swiper-container', {
        slidesPerView: 4,
        autoplay: {
          delay: 2000,
        },
        pagination: {
          el: '.swiper-pagination',
          clickable: true,
        },
        loop: true,
        paginationClickable: true,
        observer: true 
      })
    })
  },
  methods: {
    //跳转
    message(id,type,teacherid){
      this.$router.push({
        path:"/courseDetails",
        query:{
          id:id,
          type:type,
          teacher:teacherid
        }
      })
    },
  }
}
</script>
<style scoped lang="less">
  @import "~@/assets/css/list";
  .swiper-container{
    padding: 0 0 40px 0;
    margin-bottom: -20px;
  }
  //套餐详情页 课程列表
  .content2{
		.mediaBox{
			padding: 26px 0;
			&:hover{
				border-color: transparent;
				img{
					transform: scale(1.2);
				}
			}
		}
  }
  .content3{
		.mediaBox{
			padding: 26px 15px 26px 0;
			&:hover{
				border-color: transparent;
				img{
					transform: scale(1.2);
				}
			}
		}
  }
</style>



