<template>
  <div class="">
    <Head2 :isCur='4' isNavShow = true isSeachShow = true isHotLine = 'true'></Head2>
    <panel-title :crumbs="[
      {
        name:'首页',
        route:'',
      },{
        name:'课时套餐',
        route:'',
      }
    ]" />
    <section class="container" :style="{minHeight: fullHeight-82-180+'px'}">
      <!--列表页数据-->
      <div class="content">
        <div class='container' v-if='packageList.length > 0'>
          <div class="mediaBox mediaBox3 left" v-for="item in packageList" :key="item.packageid"> 
            <router-link :to="{path:'comboDetails',query: {id:item.packageid}}" class="media">
              <div class="mediaImg" v-if="picShow">
                <img :src="item.setimgpath" :alt="item.setmeal" @error = "_imageError"/>
              </div>
              <div class="mediaCon">
                <p class="mediaName line1">{{item.setmeal}}</p>
                <p class='profile'>
                  购买{{item.bughour}}课时套餐
                  <!--  
                    givestatus: 是否赠送课时（0：不赠送 1：赠送) 
                    sendvideo: "赠送录播课（0：未赠送 其他：赠送录播s课时间",
                    sendlive: "赠送直播课（0：未赠送 其他：赠送直播s课时间）",
                  -->
                  <template v-if='item.givestatus == 1'>
                    <template v-if='item.sendvideo != 0'>
                      赠送{{item.sendvideo}}课时录播课套餐
                    </template>
                    <template v-if='item.sendlive != 0'>
                      赠送{{item.sendlive}}课时直播课套餐
                    </template>
                  </template>
                </p>
                <p class='time'>
                  <span class='left'>
                    有效期：
                    <!-- efftype: 有效期状态（1：时间范围 2：固定天数） -->
                    <template v-if='item.efftype == 1'>
                      {{item.effendtime | getTime}}
                    </template>
                    <template v-if='item.efftype == 2'>
                      购买后{{item.efftime}}天
                    </template>
                  </span>
                  <span class='right'>￥{{item.setprice}}</span></p>
              </div>
            </router-link>
          </div>
        </div>
        <!-- 暂无数据 -->
        <defaultShow v-else :text="$t('Main.ClassInfo.noData')"/>
      </div>
      <!--分页-->
      <Page :total="total" :current-page='current' :limit="limit" @pagechange="pagechange" v-if = 'total!= 0'></Page>
    </section>
    <Feet></Feet>
    <v-backTop />
  </div>
</template>
<script>
import panelTitle from "@/component/panelTitle";
import Head2 from "@/component/header2";
import Server from "@/service/request";
import Path from "@/service/Path";
import Page from "@/component/page";
import Feet from "@/component/feet";
import defaultShow from "@/component/defaultShow";
import backTop from "@/component/backTop";
// import moment from "moment";
export default {
  data() {
    return {
      fullHeight:0,
      //分页
      total: 0, //总条数
      limit: 20, //每页显示条数
      current: 1, //当前页数
      packageList:[],
      picShow:false,
    };
  },
  components: {
    panelTitle,
    Head2,
    Page,
    Feet,
    defaultShow,
    'v-backTop':backTop,
  },
  created() {
    this.getPackageList(this.current);
    this.fullHeight = document.body.clientHeight;
  },
  methods: {
    //分页
    pagechange(currentPage) {
      this.getPackageList(currentPage);
    },
    getPackageList(currentPage) {
      Server.post(
        Path.getPackageList, {
          'pagenum':currentPage,
        },
        res => {
          let { code, data } = res;
          if(code === 0) {
            this.packageList = data.data;
            this.total = data.pageinfo.total;
            this.current = Number(data.pageinfo.pagenum);
          }
        }
      );
    },
    
  },
  watch:{
    packageList(val, oldVal){
      if(val.length!=0){
        this.picShow=true;
      }
    }
  },
};
</script>
<style scoped lang="less">
@import "~@/assets/css/list";
@import "~@/assets/css/base";
.classBox li.item:hover{
  cursor: inherit;
}
.classtitle {
  width: 100%;
  height: 66px;
  line-height: 66px;
  overflow: hidden;
  padding-left: 30px;
  font-size: 14px;
  color: @color2;
  span{
    color: @color1;
  }
}
</style>



